//
// Account Dashboard Title
//

.account-dashboard-title {
  background-image: url("../../assets/images/page-dashboard-heading.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-block: 2rem;

  @include media-breakpoint-up(sm) {
    padding-block: 3.5rem;
  }

  @include media-breakpoint-up(lg) {
    padding-block: 5rem;
  }

  // Container
  > .container {
    position: relative;
    z-index: 10;
  }

  &__header {
    margin-block-end: 2rem;

    @include media-breakpoint-up(lg) {
      margin-block-end: 4rem;
    }
  }
    &__intro {
      text-transform: none;
      font-style: normal;
      font-weight: normal;
      margin-block-end: 0.75rem;
    }

    &__desc {

    }

  &__stats {
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
  }
    &__stat {
      display: flex;
      gap: 1.25rem;
      align-items: center;
      flex: 1;

      &-icon {
        flex-shrink: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 3rem;
        height: 3rem;
        border-radius: $border-radius-lg;
        background-color: $bgc-600;
        color: $primary-300;

        @include media-breakpoint-up(lg) {
          width: 4rem;
          height: 4rem;
        }

        @include media-breakpoint-up(xl) {
          width: 5rem;
          height: 5rem;
        }

        svg {
          width: 50%;
          aspect-ratio: 1 / 1;
        }
      }

      &-content {
        display: flex;
        flex-direction: column;
        gap: 0.25rem;

        @include media-breakpoint-up(lg) {
          gap: 0.5rem;
        }
      }
        &-value {
          font-size: $fs-2xl;
          line-height: 1em;
          font-family: $headings-font-family;
          font-weight: bold;
          color: $fg-0;

          @include media-breakpoint-up(lg) {
            font-size: $fs-3xl;
          }

          @include media-breakpoint-up(xl) {
            font-size: $fs-4xl;
          }
        }
        &-title {
          font-size: $fs-sm;
          line-height: $lh-sm;
        }
    }

  // Performance
  &__secondary {
    display: flex;
    gap: 1.5rem;
    align-items: center;

    @include media-breakpoint-up(xxl) {
      gap: 2.25rem;
      padding-block-start: 1.375rem;
    }
  }

    &__rank-wrapper {
      width: 11rem;
      height: 11rem;
      position: relative;

      @include media-breakpoint-up(xl) {
        width: 13rem;
        height: 13rem;
      }
    }

      &__bar {
        width: 11rem;
        height: 11rem;
        fill: none;

        @include media-breakpoint-up(xl) {
          width: 13rem;
          height: 13rem;
        }

        .circle-bar-placeholder {
          stroke: $bgc-500;
          stroke-width: 1.2;
        }
        .circle-bar-fill {
          stroke: $accent-300;
          stroke-width: 1.2;
          stroke-dasharray: 69; // C=2r
          stroke-dashoffset: 17.25; // 75% (69 * 0.25)
          stroke-linecap: round;
          transform: rotate(-90deg);
          transform-origin: center;
        }
      }
      &__rank {
        position: absolute;
        inset-block-start: 50%;
        inset-inline-start: 50%;
        transform: translate(-50%, -50%);
        margin-block-start: 0.25rem;
        font-family: $headings-font-family;
        display: flex;
        flex-direction: column;
        align-items: center;

        &-number {
          font-size: 1.5rem;

          &-current {
            font-size: 3rem;
            line-height: 1em;
            color: $fg-0;
            font-weight: bold;
          }
        }
        &-label {
          font-size: $fs-sm;
          line-height: $lh-sm;
          letter-spacing: 0.1em;
          text-transform: uppercase;
        }
      }

    &__performances {
      display: flex;
      flex-direction: column;
      gap: 1rem;
    }
      &__performance {

        &-number {
          font-family: $headings-font-family;
          font-size: 1.125rem;
          line-height: 2rem;
          font-weight: bold;

          &-current {
            color: $fg-0;
            font-size: 1.75rem;
          }
        }

        &-label {
          font-size: $fs-sm;
          line-height: $lh-sm;
        }
      }
}
