//
// Ranking
//
.table-ranking {

  > tbody {

    > tr {

      &:last-child {
        > th,
        > td {
          border-block-end: 0;
        }
      }
    }
  }

  &__pos {
    width: 3rem;

    @include media-breakpoint-up(xl) {
      width: 5rem;
    }
  }

  &__rank {
    width: 1rem;

    @include media-breakpoint-up(xl) {
      width: 2rem;
    }
  }

  &__total {
    width: 18%;
  }

  &__user {
    width: 40%;
  }
}
