//
// Hero
//

.hero {
  margin-block-end: -5rem;
  position: relative;
  overflow: hidden;

  @include media-breakpoint-up(md) {
    margin-block-end: -6rem;
  }

  @include media-breakpoint-up(lg) {
    margin-block-end: -8rem;
  }

  @include media-breakpoint-up(xl) {
    margin-block-end: -9rem;
  }

  &__inner {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    text-align: center;
    padding-block: 3.5rem;

    @include media-breakpoint-up(md) {
      padding-block: 4.5rem;
    }

    @include media-breakpoint-up(lg) {
      padding-block: 7.5rem;
    }

    @include media-breakpoint-up(xl) {
      padding-block: 10rem;
    }

    @include media-breakpoint-up(xxl) {
      min-height: 764px;
    }
  }

    &__title {
      font-size: 1.5rem;
      line-height: 1.1875em;
      font-style: normal;
      margin-block-end: 0.75em;

      @include media-breakpoint-up(sm) {
        font-size: 2rem;
      }

      @include media-breakpoint-up(md) {
        font-size: 2.75rem;
      }

      @include media-breakpoint-up(lg) {
        @include font-size(4rem);
      }

      @include media-breakpoint-up(xxl) {
        margin-block-end: 2.25rem;
      }

      .text-highlight {
        display: inline-block;
        width: 100%;
        height: 1em;
        background: linear-gradient(90deg, #9747FF 34%, #1BD75E 65.13%);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;

        .ti-cursor {
          color: $accent;
          -webkit-text-fill-color: $accent;
        }
      }
    }

    &__description {
      margin-block-end: 2rem;

      @include media-breakpoint-up(md) {
        padding-inline: 5rem;
      }

      @include media-breakpoint-up(lg) {
        padding-inline: 10rem;
        margin-block-end: 3rem;
      }

      @include media-breakpoint-up(xl) {
        margin-block-end: 3rem;
        padding-inline: 6rem;
      }

      @include media-breakpoint-up(xxl) {
        margin-block-end: 3.5rem;
        padding-inline: 6.875rem;
      }
    }

    &__footer {
      display: flex;
      justify-content: center;
      gap: 1rem;
    }
}

// Decoration
.hero-decor {
  display: block;
  position: absolute;
  inset: 0;
  width: 100%;
  height: 1040px;
  stroke: rgba(255,255,255,0.05);
  mask-image: radial-gradient(100% 100% at top right, white, transparent);
  z-index: -1;
  // [mask-image:radial-gradient(100%_100%_at_top_right,white,transparent)]

  &__square {
    fill: rgba(255,255,255,0.02);
    overflow: visible;

    &--1 {
      fill: rgba(255,255,255,0.03);
      animation: 8s fadeOutIn ease-out infinite;
      animation-delay: 5s;
    }
    &--2 {
      animation: 12s fadeOutIn ease-out infinite;
      animation-delay: 1s;
    }
    &--3 {
      animation: 6s fadeOutIn ease-out infinite;
      animation-delay: 10s;
    }
    &--4 {
      animation: 10s fadeOutIn ease-out infinite;
      animation-delay: 15s;
    }
  }
}

@keyframes fadeOutIn {
  0% { opacity: 1; }
  10% { opacity: 0; }
  20% { opacity: 1; }
  100% {opacity: 1;}
}
