// Navbar brand
//
// Used for brand, project, or site names.

.navbar-brand {
  padding-block: calc(var(--#{$prefix}navbar-brand-padding-y) * 0.5);
  margin-right: var(--#{$prefix}navbar-brand-margin-end);
  font-size: $fs-2xl;
  color: var(--#{$prefix}navbar-brand-color);
  text-decoration: if($link-decoration == none, null, none);
  white-space: nowrap;
  text-transform: uppercase;
  @extend %heading;
  font-style: normal;
  margin-bottom: 0;

  @include media-breakpoint-up(lg) {
    padding-block: var(--#{$prefix}navbar-brand-padding-y);
    @include font-size(var(--#{$prefix}navbar-brand-font-size));
  }

  &:hover,
  &:focus {
    color: var(--#{$prefix}navbar-brand-hover-color);
    text-decoration: if($link-hover-decoration == underline, none, null);
  }
}

// Navbar CTA
//
// Used for Sign In and Sign Up buttons.
.navbar-cta {
  padding-block: calc(var(--#{$prefix}navbar-brand-padding-y) * 0.5);

  @include media-breakpoint-up(lg) {
    padding-block: var(--#{$prefix}navbar-brand-padding-y);
  }

  .btn {
    margin: 0;

    .btn-icon {
      width: 1.25rem;
      height: 1.25rem;
    }
  }
}
.navbar-cta-mobile {
  @include media-breakpoint-down(lg) {
    margin-block-start: 1.5rem;
    padding-block-start: 2.5rem;
    border-block-start: 1px solid (var(--#{$prefix}border-color));
  }
}


// Navbar Login Buttons
//
// Used for Sign In and Sign Up buttons.
.navbar-controls {

  .btn {
    svg {
      stroke: $fg-0;
    }
  }

  &__primary {
    position: relative;

    @include media-breakpoint-up(lg) {
      border-inline-start: 1px solid $bgc-500;
      padding-inline-start: 2rem;
      margin-inline-start: 2rem;
    }
  }

  &__secondary {
    display: flex;
    gap: 0.75rem;
  }

  &__tertiary {
    padding-inline-end: 4rem;

    .progress {
      width: 8rem;
    }
  }
}

// Navbar nav
//
// Custom navbar navigation (doesn't require `.nav`, but does make use of `.nav-link`).

.navbar-nav {

  @include media-breakpoint-up(lg) {
    border-inline-start: 1px solid var(--#{$prefix}border-color);
    min-height: 4.875rem;
  }

  .nav-item {

    @include media-breakpoint-down(lg) {
      margin-inline: -1rem;
    }

    @include media-breakpoint-up(lg) {
      display: flex;
      border-inline-end: 1px solid var(--#{$prefix}border-color);
    }

    .dropdown-menu {

      @include media-breakpoint-down(lg) {
        background-color: transparent;
        padding: 0;
        border: none;
        margin-top: 0;
      }

      @include media-breakpoint-up(lg) {
        margin-top: 0;
        margin-inline-start: calc(-1 * var(--#{$prefix}dropdown-border-width));
        border-start-start-radius: 0;
        border-start-end-radius: 0;
      }

      .dropdown-item {
        @extend %heading;
        margin-block-end: 0;
        font-style: normal;
        font-weight: normal;
        line-height: 1.125rem;

        @include media-breakpoint-down(lg) {
          border-radius: $border-radius;
          padding-inline: 2rem;
        }
      }
    }
  }

  .nav-link {
    display: flex;
    align-items: center;
    @extend %heading;
    margin-bottom: 0;
    line-height: 1.25rem;
    text-transform: uppercase;
    font-style: normal;

    @include media-breakpoint-down(lg) {
      padding-inline: 1rem;
      padding-block: 0.75rem;
      border-radius: $border-radius;

      &:hover {
        background-color: $bgc-600;
      }
    }

    &:hover {

      @include media-breakpoint-up(lg) {
        background-color: $bgc-700;
      }
    }

    &::after {
      transition: transform 0.2s ease;

      @include media-breakpoint-down(lg) {
        position: absolute;
        inset-inline-end: 0.75rem;
      }
    }

    &.active,
    &.show {
      color: var(--#{$prefix}navbar-active-color);
    }

    &.active {
      position: relative;

      &::before {

        @include media-breakpoint-up(lg) {
          content: "";
          display: block;
          position: absolute;
          inset-block-end: 0;
          inset-inline: 0;
          height: 0.25rem;
          background-color: $accent;
        }
      }
    }

    &.show {
      &::after {
        transform-origin: center;
        transform: rotate(-180deg);
      }
    }
  }

  .dropdown-menu {

  }


  // Mobile User
  &--profile {

    @include media-breakpoint-down(lg) {
      margin-inline: calc(var(--bs-offcanvas-padding-x) * -1);
      padding-inline: var(--bs-offcanvas-padding-x);
      margin-block-start: 1rem;
      padding-block-start: 1rem;
      border-block-start: 1px solid var(--#{$prefix}border-color);
    }
  }
}

// Button for toggling the navbar when in its collapsed state
.navbar-toggler {
  border: none;
}

// Keep as a separate element so folks can easily override it with another icon
// or image file as needed.
.navbar-toggler-icon {
  display: inline-block;
  width: 1.5em;
  height: 1.5em;
  vertical-align: middle;
  background-image: var(--#{$prefix}navbar-toggler-icon-bg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
}

@if $enable-dark-mode {
  @include color-mode(dark) {
    .navbar-toggler-icon {
      --#{$prefix}navbar-toggler-icon-bg: #{escape-svg($navbar-dark-toggler-icon-bg)};
    }
  }
}
