//
// Page Navigation
//
.page-nav {
  background-color: $bgc-700;
  border-block: 1px solid $bgc-500;
  display: none;
  position: sticky;
  inset-block-start: 0;
  z-index: 2;

  @include media-breakpoint-up(lg) {
    display: block;
  }

  &__nav {

  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin-block-end: 0;
    column-gap: 1rem;

    @include media-breakpoint-up(lg) {
      column-gap: 1.5rem;
    }

    @include media-breakpoint-up(xl) {
      column-gap: 2.25rem;
    }
  }

    &__item {

      > a {
        display: inline-flex;
        position: relative;
        color: $fg-0;
        font-family: $headings-font-family;
        text-transform: uppercase;
        font-size: $fs-sm;
        line-height: 1.25rem;
        font-weight: bold;
        padding-block: 0.5rem;

        @include media-breakpoint-up(lg) {
          padding-block: 1.375rem;
        }

        &:hover {
          color: $fg-200;
        }

        &::before {
          content: "";
          display: block;
          position: absolute;
          inset-inline: 0;
          inset-block-end: 0;
          height: 0.1875rem;
        }

        &.active {

          &::before {
            background-color: $accent;
          }
        }
      }
    }
}
