//
// Alert
//

.alert {
  position: relative;
  padding: var(--#{$prefix}alert-padding-y) var(--#{$prefix}alert-padding-x);
  padding-inline-end: 1rem;
  margin-bottom: var(--#{$prefix}alert-margin-bottom);
  margin-inline-start: 1.75rem;
  color: var(--#{$prefix}alert-color);
  background-color: var(--#{$prefix}alert-bg);
  border: var(--#{$prefix}alert-border);
  @include border-radius(var(--#{$prefix}alert-border-radius));

  &::before {
    content: "";
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    inset-inline-start: -1.75rem;
    inset-block-start: 50%;
    transform: translateY(-50%);
    width: 3.5rem;
    height: 3.5rem;
    border-radius: $border-radius-pill;
    background-color: $bgc-700;
    border: 0.25rem solid var(--#{$prefix}alert-icon-border-color, transparent);
  }
}

// Headings for larger alerts
.alert-heading {
  // Specified to prevent conflicts of changing $headings-color
  color: inherit;
}

// Provide class for links that match alerts
.alert-link {
  font-weight: $alert-link-font-weight;
  color: var(--#{$prefix}alert-link-color);
}


// Dismissible alerts
//
// Expand the right padding and account for the close button's positioning.

.alert-dismissible {
  padding-right: $alert-dismissible-padding-r;

  // Adjust close link position
  .btn-close {
    position: absolute;
    top: 0;
    right: 0;
    z-index: $stretched-link-z-index + 1;
    padding: $alert-padding-y * 1.25 $alert-padding-x;
  }
}

.alert-danger {

  &::before {
    border-color: $danger;
    background-image: url("data:image/svg+xml,%3Csvg width='26' height='26' viewBox='0 0 26 26' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6.5 19.5L19.5 6.5M6.5 6.5L19.5 19.5' stroke='white' stroke-width='2.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
    background-size: 1.625rem;
    background-position: center;
    background-repeat: no-repeat;
  }
}

