//
// Progress Bars
//
.progress-bars {
  display: flex;
  align-items: center;
  gap: 0.75rem;

  &__value {
    @extend %heading;
    font-size: $fs-sm;
    line-height: 1rem;
    margin-block-end: 0;
    text-transform: uppercase;
    font-style: normal;
  }

  &__holder {
    width: 8.75rem;
    display: flex;
    justify-content: space-between;
    gap: 0.25rem;
    height: .375rem;
  }

    &__bar {
      flex: 1;
      border-radius: $border-radius-pill;
      background-color: $bgc-500;

      &--primary {
        background-color: $accent;
      }

      &--warning {
        background-color: $warning;
      }

      &--danger {
        background-color: $danger;
      }
    }

  // Size: Lg
  &--lg {
    gap: 1.25rem;

    .progress-bars__holder {
      flex: 1;

      @include media-breakpoint-up(sm) {
        gap: 0.5rem;
      }
    }
  }
}
