//
// Prose
//

.prose {

  // Lists
  ol,
  ul {
    padding-inline-start: 1.25rem;

    li:not(:last-child) {
      margin-bottom: .75rem;
    }
  }

  ol,
  ul,
  dl {
    margin-top: 0;
    margin-bottom: 1.625rem;
  }

  ol ol,
  ul ul,
  ol ul,
  ul ol {
    margin-bottom: 0;
  }

  ul li::marker {
    color: $accent;
  }

  ol li::marker {
    color: $fg-0;
  }

  // Headings
  h3, .h3 {
    margin-block-end: 2rem;

    &:not(:first-child) {
      margin-block-start: 1.5rem;
    }
  }

  h4, .h4 {
    margin-block-end: 1rem;

    & {
      margin-block-start: 1.5rem;
    }
    &:first-child {
      margin-block-start: 0;
    }
  }

  // Images
  .img-center-2\/3 {
    border-radius: $border-radius-lg;
    overflow: hidden;

    @include media-breakpoint-up(lg) {
      margin-inline-start: -110px !important;
      width: calc(100% + 220px);
    }

    img {
      width: 100%;
      height: auto;
    }
  }
}


.prose p + h4,
.prose p + .h4,
.prose ul + h4,
.prose ul + .h4,
.prose ol + h4,
.prose ol + .h4 {
  margin-block-start: 3.125rem;
}
