.swiper-featured-posts {

}

.swiper-featured-posts-pagination {
  margin-block-start: 1rem;

  @include media-breakpoint-up(md) {
    margin-block-start: 2rem;
  }

  &.swiper-pagination-bullets {
    --swiper-pagination-bullet-size: 0.75rem;
    --swiper-pagination-bullet-horizontal-gap: 0.375rem;
    --swiper-pagination-bullet-inactive-color: #{$bgc-700};
    --swiper-pagination-bullet-inactive-opacity: 1;
    --swiper-pagination-color: #{$accent};
  }

  .swiper-pagination-bullet {
    border: 1px solid $bgc-500;

    &.swiper-pagination-bullet-active {
      border-color: $accent-300;
    }
  }
}

// Post Card with Image Background
.post-card-with-bg {
  width: 100%;
  aspect-ratio: 159 / 106;
  border-radius: $border-radius-lg;
  position: relative;
  overflow: hidden;
  border: var(--#{$prefix}border-width) solid var(--#{$prefix}border-color);
  isolation: isolate;

  &__img {
    display: block;
    position: absolute;
    inset-inline-start: 0;
    inset-block-start: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center center;
    margin: 0;
    pointer-events: none;
    z-index: -2;
    transition: transform 0.2s ease;
  }

  &__inner {
    position: absolute;
    inset-inline: 1.5rem;
    inset-block-end: 1.5rem;

    @include media-breakpoint-up(md) {
      inset-inline: 2.25rem;
      inset-block-end: 2.25rem;
    }
  }

  &__link {
    display: block;
    position: absolute;
    inset: 0;
    z-index: 1;

    &:hover {

      & ~ .post-card-with-bg__img {
        transform: scale(1.05);
      }
    }
  }

  &__overlay {
    display: block;
    position: absolute;
    inset: 0;
    background: linear-gradient(180deg, rgba(43, 34, 54, 0.00) 0%, rgba(43, 34, 54, 0.40) 23.50%, rgba(0, 0, 0, 0.95) 100%);
    z-index: -1;
  }

  &__meta {
    display: flex;
    flex-wrap: wrap;
    font-size: $fs-sm;
    line-height: $lh-sm;
    column-gap: 1.75rem;
    margin-block-end: 0.75rem;
  }
    &__meta-item {
      position: relative;

      &:not(:first-child)::before {
        content: "";
        display: block;
        position: absolute;
        inset-inline-start: -0.875rem;
        width: 0.25rem;
        height: 0.25rem;
        inset-block-start: 50%;
        transform: translate(-50%, -50%);
        border-radius: $border-radius-pill;
        background-color: $fg-0;
        opacity: 0.7;
      }
    }

  &__title {
    margin: 0;

    @include media-breakpoint-up(xxl) {
      padding-inline-end: 7.75rem;
    }
  }

  &__badge-wrapper {
    position: absolute;
    inset-inline-end: 1.5rem;
    inset-block-start: 1.5rem;

    @include media-breakpoint-up(md) {
      inset-inline-end: 2.25rem;
      inset-block-start: 2.25rem;
    }
  }
}
