//
// User Info
//

.user-info {
  display: flex;
  gap: 0.75rem;
  align-items: center;

  &__avatar-wrapper {
    width: 3rem;
    height: 3rem;
    position: relative;
    flex-shrink: 0;
  }

    &__circle-bar {
      position: absolute;
      width: 100%;
      height: 100%;
      margin: 0;
      z-index: 2;
      fill: none;
      stroke-width: 10;

      .circle-bar-placeholder {
        stroke: $bgc-500;
      }
      .circle-bar-fill {
        stroke: url(#GradientColor);
        stroke-width: 10;
        stroke-dasharray: 521.5; // C=2r
        // stroke-dashoffset: 130.375; // 75% (521.5 * 0.25)
        stroke-linecap: round;
        transform: rotate(-90deg);
        transform-origin: center;
      }
    }

    &__avatar {
      position: absolute;
      inset-block-start: 50%;
      inset-inline-start: 50%;
      transform: translate(-50%, -50%);
      border-radius: $border-radius-pill;
      max-width: 80%;
      height: auto;
      object-fit: cover;
    }

    &__badge {
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      inset-inline-end: -0.125rem;
      inset-block-end: 0;
      width: 1.125rem;
      height: 1.125rem;
      font-family: $headings-font-family;
      background-color: $bgc-900;
      border-radius: $border-radius-pill;
      z-index: 2;
      font-size: .625rem;
      color: $fg-0;
      line-height: 1em;
      font-weight: bold;
    }

    &__content {
      display: flex;
      flex-direction: column;
      gap: 0.125rem;
    }

      &__username {
        color: $fg-0;
        font-style: $fs-sm;
        line-height: $lh-sm;
        font-weight: bold;
      }
}
