//
// Reviews
//
.reviews {

  .review:first-of-type {
    border-block-start: 0;
    padding-block-start: 0;
  }

  .review:last-of-type {
    padding-block-end: 0;
  }
}

.review {
  padding-block: 2rem;

  @include media-breakpoint-up(lg) {
    padding-block: 2.5rem;
  }

  & + & {
    border-block-start: 1px solid $bgc-500;
  }

  &__header {
    display: flex;
    gap: 1rem;
    align-items: center;
    margin-block-end: 1.5rem;
  }

    &__avatar {
      overflow: hidden;
      border-radius: $border-radius-pill;
      flex-shrink: 0;
      margin-block-end: 0;
      width: 3rem;
      height: 3rem;
    }

    &__title {
      display: flex;
      flex-direction: column;
      gap: 0.25rem;
    }
      &__name {
        @extend %heading;
        margin-block-end: 0;
        text-transform: uppercase;
        font-size: $fs-sm;
        line-height: $lh-sm;
      }

  &__content {
    font-style: italic;
  }
}
