//
// Avatar File Form
//

.avatar-form-file {
  display: flex;
  gap: 1rem;
  flex-direction: column;

  @include media-breakpoint-up(sm) {
    flex-direction: row;
    gap: 2rem;
    align-items: center;
  }

  &__avatar {
    width: 6rem;
    height: 6rem;
    overflow: hidden;
    border-radius: $border-radius-pill;
    flex-shrink: 0;
  }
}
