//
// Footer
//

.footer {

  // Widgets
  &__widgets {
    border-block: 1px solid $bgc-500;
    padding-block: 4rem;
    background-color: $bgc-900;
    background-image: url("../../assets/images/footer-widgets-bg.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    > .container {
      position: relative;
      z-index: 4;
    }
  }

  &__widget {

    &-title {
      margin-block-end: 2rem;
    }

    &-content {
      font-size: $fs-sm;
      line-height: $lh-sm;
    }

    &-list {

      > li {

        a {
          color: $fg-200;

          &:hover {
            color: $fg-0;
          }
        }
      }
    }
  }

  // Copyright
  &__copyright {
    font-size: $fs-xs;
    line-height: $lh-xs;
    padding-block: 2.625rem;
    background-color: $bgc-900;

    // Navigation
    &-nav {

      > li {
        position: relative;
        padding-inline: 0.625rem;

        // divider
        &:not(:first-child) {

          &::before {
            content: '';
            position: absolute;
            inset-inline-start: 0;
            width: 1px;
            display: block;
            background-color: $fg-200;
            height: 0.625rem;
            top: 50%;
            transform: translateY(-50%);
          }
        }

        // link
        > a {
          color: $fg-0;

          &:hover {
            text-decoration: underline;
          }
        }
      }
    }
  }
}
