//
// Card
//

.card-footer {
  background-color: $bgc-800;
}

// Card: Base
.card--base {
  background-color: $bgc-700;

  .card-header {
    min-height: 4.75rem;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    position: relative;
    row-gap: 0.75rem;
    justify-content: center;

    @include media-breakpoint-up(xl) {
      justify-content: flex-start;
      flex-direction: row;
      align-items: center;
    }

    &__heading {
      @extend %heading;
      line-height: $h6-line-height;
      text-transform: $headings-text-transform;
      margin-block-end: 0;
    }

    &::before {
      content: "";
      display: block;
      position: absolute;
      inset-inline-start: 0;
      inset-block: 0;
      width: 0.375rem;
      background-color: $accent;
    }

    &:only-child {
      border-radius: $border-radius;
      border-block-end: none;
    }
  }

  .card-body {

  }
}

.card--base-primary {
  .card-header {
    &::before {
      background-color: $primary;
    }
  }
}

// Card: Post
.card--post {
  --#{$prefix}card-spacer-y: 1.5rem;
  --#{$prefix}card-spacer-x: 1.5rem;

  .card-img-top {
    transition: transform 0.2s ease;

    &:hover {
      transform: scale(1.05);
    }
  }

  .card-footer {
    background-color: transparent;
    border-top: 0;
    padding-block-end: var(--#{$prefix}card-spacer-y);
  }

  .card-badge {
    line-height: 1em;
    padding-block-start: var(--#{$prefix}card-spacer-y);
    padding-inline: var(--#{$prefix}card-spacer-x);
  }

  .card-img-badge {
    position: absolute;
    inset-block-start: var(--#{$prefix}card-spacer-y);
    inset-inline-end: var(--#{$prefix}card-spacer-x);
  }
}

// Card Body
.card-body__inner {
  margin-inline: calc(var(--#{$prefix}card-spacer-y) * -1);
  margin-block: calc(var(--#{$prefix}card-spacer-x) * -1);
}
