// Components
//- ----------------------------------------------
$border-width:                1px;
$border-widths: (
  1: 1px,
  2: 2px,
  3: 3px,
  4: 4px,
  5: 5px
);
$border-style:                solid;

// Define common padding and border radius sizes and more.
$border-radius:               .375rem;
$border-radius-sm:            .25rem;
$border-radius-lg:            .5rem;
$border-radius-xl:            .75rem;
$border-radius-xxl:           1.5rem;
$border-radius-pill:          50rem;

$focus-ring-width:            .25rem;
$focus-ring-opacity:          .25;
$focus-ring-blur:             0;

$caret-width:                 1rem;
// $caret-vertical-align:        $caret-width * .85 !default;
$caret-spacing:               $caret-width * .375;


// Typography
//- ----------------------------------------------
// Font, line-height, and color for body text, headings, and more.
$font-family-sans-serif:      'Open Sans', sans-serif;
$font-family-monospace:       'Fira Code', monospace;
$font-family-base:            var(--#{$prefix}font-sans-serif);
$font-family-code:            var(--#{$prefix}font-monospace);

// $font-size-root affects the value of `rem`, which is used for as well font sizes, paddings, and margins
// $font-size-base affects the font size of the body text
$font-size-root:              null !default;
$font-size-base:              1rem; // Assumes the browser default, typically `16px`
$font-size-sm:                $font-size-base * .875;
$font-size-lg:                $font-size-base * 1.25;

$font-weight-lighter:         lighter !default;
$font-weight-light:           300 !default;
$font-weight-normal:          400 !default;
$font-weight-medium:          500 !default;
$font-weight-semibold:        600 !default;
$font-weight-bold:            700;
$font-weight-bolder:          bolder !default;

$font-weight-base:            $font-weight-normal !default;

$line-height-base:            1.625;
$line-height-sm:              1.25 !default;
$line-height-lg:              2 !default;

$h1-font-size:                $font-size-base * 3;
$h2-font-size:                $font-size-base * 2.25;
$h3-font-size:                $font-size-base * 1.875;
$h4-font-size:                $font-size-base * 1.5;
$h5-font-size:                $font-size-base * 1.25;
$h6-font-size:                $font-size-base * 1.125;

$h1-line-height:              1em;
$h2-line-height:              1.11em;
$h3-line-height:              1.2em;
$h4-line-height:              1.33em;
$h5-line-height:              1.4em;
$h6-line-height:              1.55em;

$font-sizes: (
