// $accordion-button-focus-box-shadow:       $btn-focus-box-shadow !default;

$accordion-icon-width:                    1.5rem;
$accordion-icon-transition:               transform .2s ease-in-out;
$accordion-icon-transform:                rotate(-180deg) !default;



// Tooltips
//- ----------------------------------------------
$tooltip-font-size:                 $fs-xs;
$tooltip-max-width:                 200px;
$tooltip-border-radius:             var(--#{$prefix}border-radius);
$tooltip-opacity:                   .9;
$tooltip-padding-y:                 $spacer * .5;
$tooltip-padding-x:                 $spacer;
$tooltip-margin:                    10px;

$tooltip-arrow-width:               .8rem !default;
$tooltip-arrow-height:              .4rem !default;

// Form tooltips must come after regular tooltips
// scss-docs-start tooltip-feedback-variables
// $form-feedback-tooltip-padding-y:     $tooltip-padding-y !default;
// $form-feedback-tooltip-padding-x:     $tooltip-padding-x !default;
// $form-feedback-tooltip-font-size:     $tooltip-font-size !default;
// $form-feedback-tooltip-line-height:   null !default;
// $form-feedback-tooltip-opacity:       $tooltip-opacity !default;
// $form-feedback-tooltip-border-radius: $tooltip-border-radius !default;
// scss-docs-end tooltip-feedback-variables




// Breadcrumb
//- ----------------------------------------------
$breadcrumb-font-size:              null;
$breadcrumb-padding-y:              0;
$breadcrumb-padding-x:              0;
$breadcrumb-item-padding-x:         .5rem !default;
$breadcrumb-margin-bottom:          0;
$breadcrumb-bg:                     null;
$breadcrumb-divider:                quote("/");
$breadcrumb-divider-flipped:        $breadcrumb-divider;
$breadcrumb-border-radius:          0;



// Badge
//- ----------------------------------------------
$badge-font-size:                   .625rem;
$badge-font-weight:                 $font-weight-bold;
$badge-padding-y:                   .1875rem;
$badge-padding-x:                   .625rem;
$badge-border-radius:               var(--#{$prefix}border-radius-sm);

$badge-font-size-lg:                $fs-xs;
$badge-padding-y-lg:                .25rem;
$badge-padding-x-lg:                .75rem;



// Modals
//- ----------------------------------------------
$modal-inner-padding:               $spacer * 2.25;

$modal-footer-margin-between:       .5rem;

$modal-dialog-margin:               .75rem;
$modal-dialog-margin-y-sm-up:       1.75rem;

$modal-title-line-height:           null;

$modal-content-border-width:        var(--#{$prefix}border-width);
$modal-content-border-radius:       var(--#{$prefix}border-radius-lg);
$modal-content-inner-border-radius: subtract($modal-content-border-radius, $modal-content-border-width);
// $modal-content-box-shadow-xs:       $box-shadow-sm !default;
$modal-content-box-shadow-sm-up:    0px 0px 0px 0px rgba(0, 0, 0, 0.10), 0px 20px 45px 0px rgba(0, 0, 0, 0.10), 0px 81px 81px 0px rgba(0, 0, 0, 0.09), 0px 183px 110px 0px rgba(0, 0, 0, 0.05), 0px 325px 130px 0px rgba(0, 0, 0, 0.01), 0px 507px 142px 0px rgba(0, 0, 0, 0.00);

$modal-header-border-width:         $modal-content-border-width !default;
$modal-header-padding-y:            $spacer * 3;
$modal-header-padding-x:            $modal-inner-padding;
$modal-header-padding:              $modal-header-padding-y $modal-header-padding-x !default; // Keep this for backwards compatibility

$modal-footer-border-width:         $modal-header-border-width !default;

$modal-sm:                          526px;
$modal-md:                          636px;
$modal-lg:                          800px !default;
$modal-xl:                          1140px !default;

$modal-fade-transform:              translate(0, -50px) !default;
$modal-show-transform:              none !default;
$modal-transition:                  transform .3s ease-out !default;
$modal-scale-transform:             scale(1.02) !default;



// Alerts
//- ----------------------------------------------
// Define alert colors, border radius, and padding.
$alert-padding-y:               $spacer * 0.875;
$alert-padding-x:               $spacer * 3;
$alert-margin-bottom:           1rem !default;
$alert-border-radius:           var(--#{$prefix}border-radius) !default;
$alert-link-font-weight:        $font-weight-bold !default;
$alert-border-width:            var(--#{$prefix}border-width) !default;
$alert-dismissible-padding-r:   $alert-padding-x * 3 !default; // 3x covers width of x plus default padding on either side



// Progress bars
//- ----------------------------------------------
$progress-height:                   0.75rem;
$progress-font-size:                $fs-xs;
$progress-border-radius:            var(--#{$prefix}border-radius-lg);
$progress-bar-animation-timing:     1s linear infinite;
$progress-bar-transition:           width .6s ease;




// Code
//- ----------------------------------------------
$code-font-size:                    $fs-sm;

$kbd-padding-y:                     .1875rem !default;
$kbd-padding-x:                     .375rem !default;
$kbd-font-size:                     $code-font-size !default;

$pre-color:                         null !default;
