//
// Widget
//

// Widget: Categories
.category-list {
  margin-block-end: 0;
  font-family: $headings-font-family;

  .card--base & {
    margin: calc(var(--#{$prefix}card-spacer-y) * -1) calc(var(--#{$prefix}card-spacer-x) * -1);
  }

  & > &__item {
    position: relative;

    .card--base &:not(:last-child) {
      border-block-end: var(--#{$prefix}card-border-width) solid var(--#{$prefix}card-border-color);;
    }

    > a {
      color: $fg-0;
      display: flex;
      padding-inline: var(--#{$prefix}card-spacer-x);
      line-height: 1.25rem;
      padding-block: 1.5rem;

      &:hover {
        color: $link-hover-color-dark;
      }
    }

    .toggle-submenu {
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      width: 1.5rem;
      height: 1.5rem;
      padding: 0;
      inset-inline-end: calc(var(--#{$prefix}card-spacer-x) - 0.75rem);
      inset-block-start: calc(var(--#{$prefix}card-spacer-y) - 0.375rem);
      border: none;
      background-color: transparent;
      color: $fg-0;

      svg {
        width: 0.75rem;
        height: 0.75rem;
      }

      &[aria-expanded="true"] {
        svg {
          transform: rotate(180deg);
        }
      }
    }

    ul {

      .card--base & {
        background-color: $bgc-800;
        border-block-start: var(--#{$prefix}card-border-width) solid var(--#{$prefix}card-border-color);;
        padding-inline: var(--#{$prefix}card-spacer-x);

        & > li:first-child {
          padding-block-start: .75rem;
        }

        & > li:last-child {
          padding-block-end: .75rem;
        }

        a {
          color: var(--#{$prefix}body-color);
          display: flex;
          font-size: $fs-sm;
          line-height: .9375rem;
          padding-block: .75rem;

          &:hover {
            color: $link-hover-color-dark;
          }
        }
      }
    }
  }
}

// Widget: Levels & Statuses
.checkbox-options-list {

  &__item {
    display: flex;
    justify-content: space-between;
    margin-block-end: 0;
    align-items: center;

    .form-check {
      margin-block-end: 0;
    }

    &-count {
      font-family: $headings-font-family;
      line-height: 1.25em;
      font-weight: bold;
    }
  }
}
