//
// Module
//

.module {


  // Module Card
  &--card {
    display: flex;
    flex-direction: column;

    .module__figure {
      border-radius: $border-radius-lg;
      overflow: hidden;
      margin-block-end: -1.5rem;
      overflow: hidden;
      position: relative;

      img {
        aspect-ratio: 16/9;
        object-fit: cover;
        width: 100%;
        transition: transform .2s ease;

        @include media-breakpoint-up(xl) {
          aspect-ratio: 416/278;
        }
      }

      a {

        &:hover {

          img {
            transform: scale(1.05);
          }
        }
      }

      .badge {
        position: absolute;
        inset-inline-end: 1.5rem;
        inset-block-start: 1.5rem;
      }
    }

    .module__content {
      flex: 1;

      @include media-breakpoint-up(sm) {
        margin-inline: 1rem;
      }
    }
      .module__header {
        display: flex;
        flex-direction: column;
        gap: .625rem;
      }
        .module__title {
          margin-block-end: 0;

          a {
            color: $headings-color-dark;
            transition: color .2s ease;

            &:hover {
              color: $link-color-dark;
            }
          }
        }
      .module__excerpt {
        font-size: $fs-sm;
        line-height: $lh-sm;
      }

      .module__footer {
        background: transparent;
        padding-block: $card-spacer-y;
        padding-inline: 0;
        margin-inline: $card-spacer-x;
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
        .module__info {
          display: flex;
          flex-wrap: wrap;
          gap: 1rem;
          margin-block-end: 0;

          &-item {
            display: flex;
            gap: .375rem;
            align-items: center;

            svg {
              width: 1.25rem;
              height: 1.25rem;
              stroke: $fg-200;
            }

            &-label {
              @extend %heading;
              margin-block-end: 0;
              font-size: $fs-xs;
              font-style: normal;
              line-height: 1rem;
              text-transform: uppercase;
            }
          }
        }
  }

  // Module Card Simple
  &--card-plain {

    .module__figure {
      margin-block-end: 0;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      border: var(--#{$prefix}border-width) solid var(--#{$prefix}border-color);
      border-block-end: 0;
    }

    .module__content {
      border-top-left-radius: 0;
      border-top-right-radius: 0;

      @include media-breakpoint-up(sm) {
        margin-inline: 0;
      }

      .card-body {
        padding: 1.5rem;
      }
    }

    .module__title {
      font-size: $h5-font-size;
      line-height: $h5-line-height;
    }
  }

  // Module Small
  &--block-sm {
    display: flex;
    gap: 0.75rem;
    align-items: center;

    .module__figure {
      flex-shrink: 0;
      width: 4rem;
      height: 4rem;
      border-radius: $border-radius;
      margin-block-end: 0;

      img {
        max-width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: $border-radius;
      }
    }

    .module__content {
      flex: 1;
      min-width: 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

      .module__body {
        max-width: 60%;


        @include media-breakpoint-up(sm) {
          max-width: 70%;
        }
      }
        .module__title {
          font-size: 1rem;
          line-height: 1.2em;
          margin-block-end: 0;

          @include media-breakpoint-up(sm) {
            @include text-truncate;
          }

          a {
            color: $fg-0;

            &:hover {
              color: $link-hover-color-dark;
            }
          }
        }

      .module__progress {
        display: flex;
        align-items: center;
        gap: 0.5rem;

        @include media-breakpoint-up(sm) {
          gap: 0.75rem;
        }

        &-label {
          font-size: $fs-xs;
          line-height: $lh-xs;
          white-space: nowrap;
        }

        &-bar {
          width: 1.5rem;
          height: 1.5rem;
          fill: none;

          .circle-bar-placeholder {
            stroke: $bgc-500;
            stroke-width: 4px;
          }
          .circle-bar-fill {
            stroke: $accent;
            stroke-width: 3;
            stroke-dasharray: 62; // C=2r
            // stroke-dashoffset: 46.5; // 25% (62 * 0.75)
            stroke-linecap: round;
            transform: rotate(-90deg);
            transform-origin: center;
          }
        }
      }
  }
}


// Module Single
.module-single {
  display: flex;
  flex-direction: column;
  gap: 2rem;

  @include media-breakpoint-up(lg) {
    gap: 3rem;
  }
}


// Modules
.modules {

  &__title {
    font-style: normal;
    color: var(--#{$prefix}body-color);
    font-weight: normal;
    font-size: $fs-sm;
    line-height: 1.5rem;
    margin-block-end: 0.5rem;

    &:not(:first-child) {
      margin-block-start: 1.5rem;
    }
  }

  &__list {

  }
}
