//
// Accordion
//

.accordion {
  display: flex;
  flex-direction: column;
  row-gap: $spacer;
}

.accordion-button {
  @extend %heading;
  line-height: $h6-line-height;
  text-transform: $headings-text-transform;
  color: $headings-color-dark;
  margin: 0;
  padding-inline-end: 6rem;
  box-shadow: none !important;

  &::before {
    content: "";
    display: block;
    position: absolute;
    inset-inline-end: 0;
    inset-block-start: 0;
    height: 100%;
    width: 4.25rem;
    background-color: $bgc-700;
    border-top-right-radius: subtract($accordion-border-radius, $accordion-border-width);
    border-bottom-right-radius: subtract($accordion-border-radius, $accordion-border-width);
  }

  &[aria-expanded="true"]::before {
    border-bottom-right-radius: 0 !important;
  }

  &::after {
    display: block;
    position: absolute;
    inset-inline-end: 1.375rem;
    inset-block-start: (50% - #{$accordion-icon-width});
  }
}

.accordion-item {
  border: var(--#{$prefix}accordion-border-width) solid var(--#{$prefix}accordion-border-color);
  border-radius: var(--#{$prefix}accordion-border-radius);
  overflow: hidden;

  &:first-of-type {
    border-radius: var(--#{$prefix}accordion-border-radius);

    .accordion-button {

      &.collapsed {
        border-radius: var(--#{$prefix}accordion-border-radius);
      }
    }
  }

  &:not(:first-of-type) {
    border: var(--#{$prefix}accordion-border-width) solid var(--#{$prefix}accordion-border-color);
  }

  &:last-of-type {
    border-radius: var(--#{$prefix}accordion-border-radius);

    .accordion-button {
      border-radius: var(--#{$prefix}accordion-border-radius);

      &.collapsed {
        border-radius: var(--#{$prefix}accordion-border-radius);
      }

      &[aria-expanded="true"] {
        border-start-start-radius: var(--#{$prefix}accordion-border-radius);
        border-start-end-radius: var(--#{$prefix}accordion-border-radius);
        border-end-start-radius: 0;
        border-end-end-radius: 0;
      }
    }

    .accordion-collapse {
      border-radius: var(--#{$prefix}accordion-border-radius);
    }
  }
}

.accordion-body {
  border-top: var(--#{$prefix}accordion-border-width) solid var(--#{$prefix}accordion-border-color);
}


// Stacked
.accordion--stacked {
  row-gap: 0;

  .accordion-item {
    background-color: $bgc-700;

    &:not(:last-of-type) {
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }

    &:not(:first-of-type) {
      border-top-left-radius: 0;
      border-top-right-radius: 0;
      margin-block-start: -1px;
    }

    .accordion-button {
      padding-inline-end: 1.5rem;
      padding-inline-start: 3.75rem;

      &::before {
        background-color: transparent;
        inset-inline-end: auto;
        inset-inline-start: 0;
      }
      &::after {
        inset-inline-end: auto;
        inset-inline-start: 1.5rem;
        inset-block-start: 1.625rem;
      }
    }
  }

  .accordion-button {
    background-color: $bgc-700;

    @include media-breakpoint-down(lg) {
      flex-direction: column;
      align-items: flex-start;
      gap: 1.5rem;
    }

    @include media-breakpoint-up(lg) {
      justify-content: space-between;
    }

    &::before {
      width: 0.375rem;
      border-radius: 0;
      transition: background-color 0.2s ease;
    }

    &[aria-expanded="true"]::before {
      background-color: $accent;
    }

    &__heading {
      display: flex;
      flex-direction: column;
      gap: 0.25rem;
    }

      &__title {
        margin-block-end: 0;
      }

      &__desc {
        font-family: $font-family-sans-serif;
        font-size: $fs-sm;
        line-height: $lh-sm;
        font-style: normal;
        font-weight: normal;
        text-transform: none;
        color: $fg-200;
      }

      &__info {
        display: flex;
        gap: 1rem;
        margin-block-end: 0;

        &-item {
          display: flex;
          gap: 0.5rem;
          align-items: center;
          font-size: $fs-xs;
          line-height: 1rem;
          font-style: normal;
          font-weight: normal;

          svg {
            width: 1.25rem;
            height: 1.25rem;
            stroke: $fg-200;
          }
        }
      }
  }
}
