//
// Video
//

// Video JS
.video-js {
  aspect-ratio: 1240/693;
  width: 100%;
  height: auto;
  border-radius: $border-radius-lg;
  overflow: hidden;

  .vjs-big-play-button {
    width: 9rem;
    height: 5rem;
    margin: 0 !important;
    transform: translate(-50%, -50%);
    border-color: transparent !important;
    background-color: rgba(0,0,0,.7);

    .vjs-icon-placeholder {

      &::before {
        width: 2.5rem;
        height: 2.5rem;
        inset-inline-start: 50%;
        inset-block-start: 50%;
        transform: translate(-50%, -50%);
        font-size: 4rem;
        margin: -0.125rem 0 0 -0.875rem;
      }
    }
  }

  &:hover {
    .vjs-big-play-button {
      background-color: rgba(0,0,0,.8);
    }
  }
}

.vjs-poster {

  &::before {
    content: "";
    display: block;
    position: absolute;
    inset: 0;
    background-color: $black;
    opacity: .3;
  }
}

// Video Box
.video-box {

  .card-body {
    @include media-breakpoint-down(md) {
      padding: 1rem;
    }
  }

  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-block-start: 0.75rem;

    @include media-breakpoint-up(lg) {
      padding-block-start: 1.25rem;
    }
  }

    &__info {
      display: flex;
      flex-wrap: wrap;
      gap: 1rem;
      margin-block-end: 0;

      &-item {
        display: flex;
        gap: .375rem;
        align-items: center;

        svg {
          width: 1.25rem;
          height: 1.25rem;
          stroke: $fg-200;
        }

        &-label {
          @extend %heading;
          margin-block-end: 0;
          font-size: $fs-xs;
          font-style: normal;
          line-height: 1rem;
          text-transform: uppercase;
        }
      }
    }
}
