//
// Account Nav
//
.account-nav {

  &__list {
    @include list-unstyled;
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
    margin-block-end: 0;

    & + .account-nav__list {
      margin-block-start: $spacer;
      padding-block-start: $spacer;
      border-block-start: 1px solid (var(--#{$prefix}border-color));
    }
  }

    &__item {

    }

      &__link {
        display: flex;
        align-items: center;
        gap: 0.75rem;
        padding-inline: 0.75rem;
        padding-block: 0.625rem;
        border-radius: $border-radius;

        &:hover {
          background-color: $bgc-600;
        }

        &.active {
          background-color: $bgc-700;

          .account-nav__icon {
            stroke: $accent;
          }
          .account-nav__label {
            color: $accent;
          }

          &:hover {
            background-color: $bgc-600;
          }
        }
      }

        &__icon {
          width: 1.5rem;
          height: 1.5rem;
          flex-shrink: 0;
          stroke: $fg-200;
          stroke-width: 1.5px;
        }

        &__label {
          color: $fg-0;
          font-family: $headings-font-family;
          font-size: 1rem;
          line-height: 1.5em;
          @include text-truncate;
        }

  // Social Networks
  &--social {

    .account-nav__icon {
      stroke: none;
      fill: $fg-200;
    }
  }
}
