//
// Dropdown
//

.dropdown-header {
  display: block;
  // padding: var(--#{$prefix}dropdown-header-padding-y) var(--#{$prefix}dropdown-header-padding-x);
  @include font-size($fs-xs);
  margin-bottom: 0; // for use with heading elements
  font-style: normal;
}


.dropdown-menu--profile {
  border-radius: $border-radius-xl;
  box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.10), 0px 9px 20px 0px rgba(0, 0, 0, 0.10), 0px 36px 36px 0px rgba(0, 0, 0, 0.09), 0px 81px 49px 0px rgba(0, 0, 0, 0.05), 0px 144px 58px 0px rgba(0, 0, 0, 0.01), 0px 225px 63px 0px rgba(0, 0, 0, 0.00);
  margin-block-start: 0.75rem !important;

  .dropdown-item {
    font-family: $headings-font-family;
    padding-block: 0.25rem;
    font-weight: bold;
  }
  .dropdown-header {
    margin-block-start: 1rem;
  }

  li:first-child {
    .dropdown-header {
      margin-block-start: 0;
    }
  }
}


// User info
.dropdown-user {
  display: flex;
  gap: 0.75rem;
  padding: var(--#{$prefix}dropdown-item-padding-y) var(--#{$prefix}dropdown-item-padding-x);
  align-items: center;

  &__avatar-wrapper {
    width: 3rem;
    height: 3rem;
    position: relative;
    flex-shrink: 0;
  }

    &__circle-bar {
      position: absolute;
      width: 100%;
      height: 100%;
      margin: 0;
      z-index: 2;
      fill: none;
      stroke-width: 10;

      .circle-bar-placeholder {
        stroke: $bgc-500;
      }
      .circle-bar-fill {
        stroke: url(#GradientColor);
        stroke-width: 10;
        stroke-dasharray: 521.5; // C=2r
        stroke-dashoffset: 130.375; // 75% (521.5 * 0.25)
        stroke-linecap: round;
        transform: rotate(-90deg);
        transform-origin: center;
      }
    }

    &__avatar {
      position: absolute;
      inset-block-start: 50%;
      inset-inline-start: 50%;
      transform: translate(-50%, -50%);
      border-radius: $border-radius-pill;
      max-width: 80%;
      height: auto;
      object-fit: cover;
    }

    &__badge {
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      inset-inline-end: -0.125rem;
      inset-block-end: 0;
      width: 1.125rem;
      height: 1.125rem;
      font-family: $headings-font-family;
      background-color: $bgc-900;
      border-radius: $border-radius-pill;
      z-index: 2;
      font-size: .625rem;
      color: $fg-0;
      line-height: 1em;
      font-weight: bold;
    }

    &__content {
      display: flex;
      flex-direction: column;
      gap: 0.125rem;
    }

      &__name {
        color: $fg-0;
        font-family: $headings-font-family;
        font-style: $fs-sm;
        line-height: 1rem;
        text-transform: uppercase;
        font-weight: bold;
      }

      &__username {
        color: $fg-0;
        font-family: $headings-font-family;
        font-size: $fs-xs;
        line-height: .875rem;
      }
}
