//
// Modals
//

// Modal content
.modal-content {
  position: relative;

  .btn-close {
    position: absolute;
    inset-block-start: 0;
    inset-inline-end: 0;
    z-index: 5;
    transform: translate(50%, -50%);

    @include media-breakpoint-down(sm) {
      width: 1.5rem;
      height: 1.5rem;
    }
  }
}

// Modal header
.modal-header {
  background-image: url("../../assets/images/page-title-bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  overflow: hidden;

  @include media-breakpoint-down(sm) {
    padding-inline: calc(var(--#{$prefix}modal-padding) * 0.6);
    padding-block: calc(var(--#{$prefix}modal-padding) * 0.85);
  }

  .btn-close {

  }

  & > *:not([class*="effect-duotone"]) {
    position: relative;
    z-index: 4;
  }
}

// Modal Body
.modal-body {

  @include media-breakpoint-down(sm) {
    padding: calc(var(--#{$prefix}modal-padding) * 0.6);
  }
}
