//
// Table
//

.table {
  font-size: $fs-sm;
  line-height: $lh-sm;

  > :not(caption) > * > * {

    // @include media-breakpoint-down(md) {
    //   padding: calc($table-cell-padding-y * 0.65) calc($table-cell-padding-x * 0.65);
    // }
  }

  > thead {

    > tr {

      > th {
        font-size: $fs-xs;
        line-height: $lh-xs;
        text-transform: uppercase;
        font-family: $headings-font-family;
        letter-spacing: 0.1em;
        color: $table-accent-color;
        background-color: $bgc-800;
      }
    }
  }

  > tbody {

    > tr {

      > th {
        font-weight: normal;
      }
    }
  }
}
