//
// Course
//

.course {

  // Block: Course v1
  &--block {

    .course__inner {
      display: flex;
      gap: 1.5rem;
      align-items: stretch;

      @include media-breakpoint-up(lg) {
        gap: 2.25rem;
      }
    }

      .course__figure {
        background-color: $bgc-900;
        border-radius: $border-radius;
        flex-basis: 3rem;
        padding-block: 0.5rem;
        display: flex;
        align-self: flex-start;
        align-items: center;
        justify-content: center;
        flex-shrink: 0;
        margin-block-end: 0;
        position: relative;

        @include media-breakpoint-up(sm) {
          flex-basis: 5rem;
          padding-block: 1rem;
        }

        @include media-breakpoint-up(lg) {
          flex-basis: 8rem;
          padding-block: 1rem;
          align-self: stretch;
        }

        img {
          max-width: 60%;
          height: auto;
        }

        .badge {
          position: absolute;
          inset-inline-start: -.875rem;
          inset-block-start: .625rem;
        }
      }

      .course__content {
        flex: 1;
        display: flex;
        flex-direction: column;
        gap: 1.5rem;
        min-width: 0;

        @include media-breakpoint-up(xl) {
          flex-direction: row;
        }
      }
        .course__body {
          display: flex;
          flex-direction: column;
          gap: 0.75rem;
          flex: 1;
          min-width: 0;
        }

          .course__heading {
            display: flex;
            flex-direction: column;
            gap: 0.625rem;
          }

            .course__title {
              margin-block-end: 0;

              @include media-breakpoint-up(lg) {
                @include text-truncate();
              }

              a {
                color: $fg-0;

                &:hover {
                  color: $link-hover-color-dark;
                }
              }
            }

          .course__info {
            display: flex;
            flex-wrap: wrap;
            gap: 1rem;
            padding-block-start: .75rem;
            margin-block-end: 0;

            &-item {
              display: flex;
              gap: .375rem;
              align-items: center;

              svg {
                width: 1.25rem;
                height: 1.25rem;
                stroke: $fg-200;
              }

              &-label {
                @extend %heading;
                margin-block-end: 0;
                font-size: $fs-xs;
                font-style: normal;
                line-height: 1rem;
                text-transform: uppercase;
              }
            }
          }

        .course__cta {
          flex-shrink: 0;
          display: flex;
          flex-wrap: wrap;
          gap: .75rem;
          border-block-start: var(--#{$prefix}border-width) solid var(--#{$prefix}border-color);
          padding-block-start: 1.5rem;

          @include media-breakpoint-up(xl) {
            padding-inline-start: 2rem;
            padding-block-start: 0;
            border-block-start: 0;
            border-inline-start: var(--#{$prefix}border-width) solid var(--#{$prefix}border-color);
            margin-inline-start: auto;
            align-self: center;
          }
        }
  }

  // Block: Course (sm) v2
  &--block-sm {
    display: flex;
    gap: 1rem;
    align-items: center;

    .course__figure {
      flex-shrink: 0;
      background-color: $bgc-900;
      border-radius: $border-radius;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 4rem;
      height: 4rem;
      margin-block: 0;
      position: relative;

      img {
        max-width: 62.5%;
        height: auto;
      }
    }
      .course__badge {
        width: 1.25rem;
        height: 1.25rem;
        border-radius: $border-radius-pill;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        inset-inline-start: -0.3125rem;
        inset-block-start: -0.3125rem;
        background-color: $primary;
        color: $fg-0;

        svg {
          width: 0.75rem;
          height: 0.75rem;
        }
      }

    .course__content {
      flex: 1;
      min-width: 0;
    }
      .course__heading {
        display: flex;
        flex-direction: column;
        gap: 0.125rem;
      }
        .course__title {
          margin-block-end: 0;
          @include text-truncate;

          a {
            color: $fg-0;

            &:hover {
              color: $link-hover-color-dark;
            }
          }
        }
        .course__meta {
          margin-block-end: 0;
          display: flex;
          font-size: $fs-sm;
          line-height: 1em;
          gap: 0.625rem;
          font-family: $headings-font-family;
        }

    .course__progress {
      display: flex;
      height: 100%;
      align-items: center;
      gap: 0.75rem;

      .progress {
        width: 100%;
      }

      &-label {
        @extend %heading;
        margin-block-end: 0;
        font-size: $fs-sm;
        font-style: normal;
        line-height: 1rem;
        text-transform: uppercase;
        min-width: 2.125rem;
      }
      &-percentage {
        min-width: 2rem;
        text-align: end;
        white-space: nowrap;
        font-size: $fs-sm;
        color: $fg-0;
        line-height: 1.25rem;
        font-family: $headings-font-family;
      }
    }

    .course__info {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      height: 100%;
      gap: 1rem;
      margin-block-end: 0;

      @include media-breakpoint-up(lg) {
        justify-content: flex-end;
      }

      &-item {
        @extend %heading;
        margin-block-end: 0;
        font-style: normal;
        font-size: $fs-xs;
        line-height: 1em;
        display: flex;
        align-items: center;
        gap: 0.375rem;
        color: $fg-0;

        svg {
          width: 1.25rem;
          height: 1.25rem;
          stroke: $fg-200;
        }
      }
    }
  }
}


// Coures List
.courses-list {
  display: flex;
  flex-direction: column;

  .card & {
    margin-block: calc(var(--#{$prefix}card-spacer-y) * -1);
  }

  .course--block-sm {
    margin-inline: calc(var(--#{$prefix}card-spacer-x) * -1);
    padding-block: 1.5rem;
    padding-inline: 1.75rem;

    &:not(:last-child) {
      border-block-end: var(--#{$prefix}border-width) solid var(--#{$prefix}border-color);
    }
  }
}


// Course Info
.course-info {
  background-color: $bgc-700;
  border-radius: $border-radius-lg;
  border: var(--#{$prefix}border-width) solid var(--#{$prefix}border-color);
  padding: 1.5rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1.75rem;

  &__content {
    display: flex;
    flex-direction: column;
    gap: 1.75rem;

    @include media-breakpoint-up(md) {
      flex: 1;
    }

    @include media-breakpoint-up(lg) {
      gap: 3.25rem;
    }

    @include media-breakpoint-up(xl) {
      padding-inline: 1rem;
      padding-block-start: 0.5rem;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: 1fr 2fr;
    font-size: $fs-sm;
    line-height: $lh-sm;
    gap: 1.5rem;
    margin-block-end: 0;

    dt {
      font-weight: normal;
    }

    dd {
      text-align: end;
      margin-block-end: 0;
      color: $fg-0;

      svg {
        width: 1.25rem;
        height: 1.25rem;
        stroke: $fg-200;
      }
    }
  }
}
