//
// Header
//

.header {

  // Topbar
  &__topbar {
    color: $fg-0;

    &-inner {
      padding-block: .625rem;

      a {
        font-weight: bold;
        color: $fg-0;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }

  // Nav
  &__nav {

  }

  // Search
  &__search {
    position: relative;

    @include media-breakpoint-down(lg) {
      margin-block-start: 1rem;
    }

    .form-control {
      width: 100%;
      padding-inline-start: 3.25rem;

      @include media-breakpoint-up(lg) {
        width: 15rem;
      }

      @include media-breakpoint-up(xxl) {
        width: 20rem;
      }
    }

    .btn-submit {
      position: absolute;
      inset-inline-start: 0.375rem;
      inset-block-start: 50%;
      transform: translateY(-50%);
      padding-inline: 0;
      width: 3rem;

      svg {
        width: 1.25rem;
        height: 1.25rem;
        stroke: $fg-0;
      }
    }
  }
}

// Dark
@include color-mode(dark) {
  .header {
    // Nav
    &__nav {
      background-color: $bgc-800;
      border-block: 1px solid $border-color-dark;

      @include media-breakpoint-down(lg) {
        border-block-start: none;
      }
    }
  }
}
