.post-meta {
  display: flex;
  flex-wrap: wrap;
  font-size: $fs-sm;
  line-height: $lh-sm;
  column-gap: 1.75rem;
  margin-block-end: 0.75rem;

  &__item {
    position: relative;

    &:not(:first-child)::before {
      content: "";
      display: block;
      position: absolute;
      inset-inline-start: -0.875rem;
      width: 0.25rem;
      height: 0.25rem;
      inset-block-start: 50%;
      transform: translate(-50%, -50%);
      border-radius: $border-radius-pill;
      background-color: $fg-0;
      opacity: 0.7;
    }
  }
}
