//
// Progress bars
//

.progress-title {
  @extend %heading;
  font-style: normal;
  text-transform: uppercase;

  &--sm {
    font-size: $fs-xs;
    line-height: 1rem;
    margin-block-end: 0.25rem;
  }

  &--lg {
    font-size: $fs-base;
    line-height: 1.25em;
    margin-block-end: 0.75rem;
  }
}
.progress-bar {
  @include border-radius(var(--#{$prefix}progress-border-radius));
}
.progress--sm {
  height: 0.5rem;
}
