// $enable-deprecation-messages: true !default;
// $enable-important-utilities:  true !default;

$enable-dark-mode:            true;
$color-mode-type:             data;


//- Base
//- ----------------------------------------------


//- Colors
//- ----------------------------------------------
// Primitive Colors
$plum-950:    #1d1429;
$plum-900:    #2b2236;
$plum-800:    #362b45;
$plum-700:    #392d49;
$plum-600:    #403351;
$plum-500:    #4b3b60;

$gray-200:    #a59cae;
$gray-300:    #8c8297;
$gray-400:    #7a7283;

$violet-300:  #8040f5;
$violet-400:  #6a3bc0;

$green-300:   #00ff5b;
$green-400:   #1bd75e;

$red-400:     #f92552;

$yellow-400:  #ffb400;
$white:       #fff;
$black:       #000;

// Semantic Colors
$primary:     $violet-400;
$primary-300: $violet-300;
$primary-400: $violet-400; // alias to $primary
$secondary:   $plum-500;
$accent:      $green-400;
$accent-300:  $green-300;
$accent-400:  $green-400; // alias to $accent
$success:     $green-400;
$info:        $violet-400;
$warning:     $yellow-400;
$danger:      $red-400;
$light:       $white;
$dark:        $black;

// Background colors
$bgc-950:     $plum-950;
$bgc-900:     $plum-900;
$bgc-800:     $plum-800;
$bgc-700:     $plum-700;
$bgc-600:     $plum-600;
$bgc-500:     $plum-500;

// Foreground colors
$fg-900:      $dark;
$fg-400:      $gray-400;
$fg-300:      $gray-300;
$fg-200:      $gray-200;
$fg-0:        $light;


$theme-colors: (
  "primary-300": $primary-300,
  "primary":     $primary,
  "accent-300":  $accent-300,
  "accent":      $accent,
  "secondary":   $secondary,
  "success":     $success,
  "info":        $info,
  "warning":     $warning,
  "danger":      $danger,
  "light":       $light,
  "dark":        $dark
);


//- Body
//- ----------------------------------------------
// Settings for the `<body>` element.

$body-color-dark:              $fg-200;
$body-bg-dark:                 $bgc-900;

// $body-secondary-color:      rgba($body-color, .75) !default;
// $body-secondary-bg:         $gray-200 !default;

// $body-tertiary-color:       rgba($body-color, .5) !default;
// $body-tertiary-bg:          $gray-100 !default;

// $body-emphasis-color:       $black !default;


//- Links
//- ----------------------------------------------
// Style anchor elements.
$link-color-dark:                         $accent;
$link-hover-color-dark:                   $accent-300;


// Components
//- ----------------------------------------------
// Define common padding and border radius sizes and more.
$border-color-dark:                $bgc-500;

$focus-ring-color:                 rgba($primary, $focus-ring-opacity);
$focus-ring-box-shadow:            0 0 $focus-ring-blur $focus-ring-width $focus-ring-color;


// Typography
//- ----------------------------------------------
$headings-color-dark:              $fg-0;


// Tables
//- ----------------------------------------------
$table-color:                 var(--#{$prefix}body-color);
$table-bg:                    $bgc-700;
$table-accent-bg:             transparent !default;
$table-accent-color:          $fg-0;

// $table-striped-color:         $table-color !default;
// $table-striped-bg-factor:     .05 !default;
// $table-striped-bg:            rgba($black, $table-striped-bg-factor) !default;

// $table-active-color:          $table-color !default;
// $table-active-bg-factor:      .1 !default;
// $table-active-bg:             rgba($black, $table-active-bg-factor) !default;

// $table-hover-color:           $table-color !default;
// $table-hover-bg-factor:       .075 !default;
// $table-hover-bg:              rgba($black, $table-hover-bg-factor) !default;

$table-border-factor:         .1 !default;
$table-border-color:          var(--#{$prefix}border-color) !default;

// $table-group-separator-color: currentcolor !default;

// $table-caption-color:         var(--#{$prefix}secondary-color) !default;

// $table-variants: (
//   "primary":    shift-color($primary, $table-bg-scale),
//   "secondary":  shift-color($secondary, $table-bg-scale),
//   "success":    shift-color($success, $table-bg-scale),
//   "info":       shift-color($info, $table-bg-scale),
//   "warning":    shift-color($warning, $table-bg-scale),
//   "danger":     shift-color($danger, $table-bg-scale),
//   "light":      $light,
//   "dark":       $dark,
// ) !default;


// Buttons + Forms
//- ----------------------------------------------
// Shared variables that are reassigned to `$input-` and `$btn-` specific variables.
$input-btn-focus-color:             $focus-ring-color;
$input-btn-focus-box-shadow:        $focus-ring-box-shadow;


// Buttons
//- ----------------------------------------------
// For each of Bootstrap's buttons, define text, background, and border color.
$btn-box-shadow:                    inset 0 1px 0 rgba($white, .15), 0 1px 1px rgba($black, .075);
$btn-focus-box-shadow:              $input-btn-focus-box-shadow;
$btn-active-box-shadow:             inset 0 3px 5px rgba($black, .125);

$btn-link-color:                    $fg-0;
$btn-link-hover-color:              $fg-200;
$btn-link-disabled-color:           $gray-400;



// Forms
//- ----------------------------------------------
$input-bg:                              var(--#{$prefix}body-bg);
$input-disabled-color:                  null;
$input-disabled-bg:                     var(--#{$prefix}secondary-bg);
$input-disabled-border-color:           null;

$input-color:                           var(--#{$prefix}body-color) !default;
$input-border-color:                    var(--#{$prefix}border-color) !default;
// $input-box-shadow:                      $box-shadow-inset !default;

$input-focus-bg:                        $input-bg;
$input-focus-border-color:              $primary-300;
$input-focus-color:                     $fg-0;
$input-focus-width:                     $input-btn-focus-width;
$input-focus-box-shadow:                $input-btn-focus-box-shadow;

$input-placeholder-color:               $fg-300;
$input-plaintext-color:                 var(--#{$prefix}body-color) !default;

$form-label-color:                      $fg-0;

$form-check-input-bg:                   $input-bg;
$form-check-input-focus-border:         $input-focus-border-color;
$form-check-input-focus-box-shadow:     $focus-ring-box-shadow;

$form-check-label-color:                $headings-color-dark;

$form-check-input-checked-color:        $fg-0;
