$form-file-button-color:          $fg-0;
$form-file-button-bg:             $bgc-700;
$form-file-button-hover-bg:       $bgc-600;



// Close
//- ----------------------------------------------
$btn-close-color:            $fg-0;
$btn-close-bg-color:         $primary;
$btn-close-bg:               url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='#{$btn-close-color}'><path d='M.293.293a1 1 0 0 1 1.414 0L8 6.586 14.293.293a1 1 0 1 1 1.414 1.414L9.414 8l6.293 6.293a1 1 0 0 1-1.414 1.414L8 9.414l-6.293 6.293a1 1 0 0 1-1.414-1.414L6.586 8 .293 1.707a1 1 0 0 1 0-1.414z'/></svg>") !default;
$btn-close-focus-shadow:     none;
$btn-close-white-filter:     none;



// Offcanvas
//- ----------------------------------------------
$offcanvas-border-color:     $bgc-700;


// Navbar
//- ----------------------------------------------
$navbar-dark-color:                 rgba($white, .55) !default;
$navbar-dark-hover-color:           rgba($white, .75) !default;
$navbar-dark-active-color:          $white !default;
$navbar-dark-disabled-color:        rgba($white, .25) !default;
$navbar-dark-icon-color:            $navbar-dark-color !default;
$navbar-dark-toggler-icon-bg:       url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 24 24' stroke-width='1.5' stroke='#{$fg-0}'><path stroke-linecap='round' stroke-linejoin='round' d='M3.75 6.75h16.5M3.75 12h16.5m-16.5 5.25h16.5' /></svg>");
$navbar-dark-toggler-border-color:  rgba($white, 0);
$navbar-dark-brand-color:           $navbar-dark-active-color !default;
$navbar-dark-brand-hover-color:     $navbar-dark-active-color !default;



// Cards
//- ----------------------------------------------
$card-title-color:                  $headings-color-dark;
$card-subtitle-color:               null;
$card-border-color:                 var(--#{$prefix}border-color);
$card-cap-bg:                       $bgc-600;
$card-cap-color:                    $headings-color-dark;



// Accordion
//- ----------------------------------------------
