// Tooltips
//- ----------------------------------------------
$tooltip-color:                     $fg-0;
$tooltip-bg:                        $bgc-950;



// Dropdowns
//- ----------------------------------------------
// Dropdown menu container and contents.
$dropdown-color:                    var(--#{$prefix}body-color);
$dropdown-bg:                       $bgc-700;
$dropdown-border-color:             var(--#{$prefix}border-color);
$dropdown-divider-bg:               $dropdown-border-color;
$dropdown-box-shadow:               0px 0px 0px 0px rgba(0, 0, 0, 0.10), 0px 9px 20px 0px rgba(0, 0, 0, 0.10), 0px 36px 36px 0px rgba(0, 0, 0, 0.09), 0px 81px 49px 0px rgba(0, 0, 0, 0.05), 0px 144px 58px 0px rgba(0, 0, 0, 0.01), 0px 225px 63px 0px rgba(0, 0, 0, 0.00);
$dropdown-link-color:               $fg-0;
$dropdown-link-hover-color:         $link-hover-color-dark;
$dropdown-link-hover-bg:            $bgc-600;

$dropdown-link-active-color:        $accent;
$dropdown-link-active-bg:           $bgc-600;

$dropdown-link-disabled-color:      var(--#{$prefix}tertiary-color) !default;

$dropdown-header-color:             $body-color-dark;


// Pagination
//- ----------------------------------------------
$pagination-color:                  $fg-0;
$pagination-bg:                     $bgc-700;

$pagination-border-color:           var(--#{$prefix}border-color);

$pagination-focus-color:            var(--#{$prefix}link-hover-color);
$pagination-focus-bg:               var(--#{$prefix}secondary-bg);
$pagination-focus-box-shadow:       none;
$pagination-focus-outline:          0;

$pagination-hover-color:            $fg-0;
$pagination-hover-bg:               $accent;
$pagination-hover-border-color:     $accent-300;

$pagination-active-color:           $fg-0;
$pagination-active-bg:              $accent;
$pagination-active-border-color:    $accent-300;

$pagination-disabled-color:         $fg-300;
$pagination-disabled-bg:            $bgc-500;
$pagination-disabled-border-color:  $bgc-500;


// Breadcrumb
//- ----------------------------------------------
$breadcrumb-divider-color:          $fg-0;
$breadcrumb-active-color:           $fg-0;



// Badge
//- ----------------------------------------------
$badge-color:                       $fg-0;



// Modals
//- ----------------------------------------------
$modal-content-color:               null !default;
