//
// Statistics
//

.statistics {

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
    position: relative;

    &::before {

      @include media-breakpoint-up(md) {
        content: "";
        display: block;
        position: absolute;
        inset-inline-start: 0;
        inset-block: 0;
        width: 1px;
        background-color: $bgc-500;
      }
    }

    [class*="col-"]:first-child &::before {
      display: none;
    }
  }

    &__title {
      margin-block-end: 0;
      font-style: normal;
      font-weight: normal;
      text-transform: none;
      @include font-size(2.5rem);

      strong {
        font-weight: 500;
      }
    }

    &__desc {

    }
}
