//
// Duotone effect
//

.effect-duotone {
  position: relative;
  isolation: isolate;

  &__layer {

    &::before,
    &::after {
      content: "";
    }

    &,
    &::before,
    &::after {
      position: absolute;
      inset: 0;
    }

    &::before {
      background-color: $black;
      mix-blend-mode: color;
      z-index: 1;
    }

    &::after {
      mix-blend-mode: lighten;
      z-index: 3;
    }

    &-inner {
      display: block;
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      z-index: 2;
      mix-blend-mode: multiply;
    }
  }

  // Overlay
  &__overlay {
    position: absolute;
    inset: 0;
    background-color: $black;
    opacity: .2;
    z-index: 3;
    pointer-events: none;
  }

  // Dots
  &__dots {
    content: "";
    display: block;
    position: absolute;
    inset: 0;
    background-image: url("../../assets/images/dot-4x4.png");
    background-size: 4px 4px;
    background-position: 100% 0;
    background-repeat: repeat;
    opacity: 1;

    @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
      background-image: url("../../assets/images/dot-4x4@2x.png");
    }
  }

  // Variations: Base
  &--base {
    .effect-duotone__layer::after {
      background-color: $plum-800;
      mix-blend-mode: screen;
    }

    .effect-duotone__layer-inner {
      background-color: $plum-950;
    }
  }

  // Variations: Accent
  &--accent {
    .effect-duotone__layer::after {
      background-color: $plum-950;
      mix-blend-mode: lighten;
    }

    .effect-duotone__layer-inner {
      background-color: $accent-300;
    }

    .effect-duotone__overlay {
      opacity: .3;
    }
  }

  // Variations: Primary
  &--primary {
    .effect-duotone__layer::after {
      background-color: $plum-800;
      mix-blend-mode: screen;
    }

    .effect-duotone__layer-inner {
      background-color: $primary;
    }

    .effect-duotone__overlay {
      opacity: .5;
    }
  }
}
