//
// Stat Box
//
.statbox {
  border: 1px solid (var(--#{$prefix}border-color));
  border-radius: $border-radius-lg;
  padding: 1.5rem;
  display: flex;
  gap: 1.25rem;

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    width: 3.5rem;
    height: 3.5rem;
    border-radius: $border-radius;
    background-color: $primary-300;

    svg {
      width: 2rem;
      height: 2rem;
      color: $fg-0;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
  }
    &__title {
      margin-block-end: 0;
      font-style: normal;
      line-height: 1.75rem;
    }
    &__subtitle {
      font-size: $fs-sm;
      line-height: $lh-sm;
    }
}
