//
// Course Title
//

.course-title {
  position: relative;
  isolation: isolate;
  padding-block: 2rem;

  @include media-breakpoint-up(md) {
    padding-block: 3rem;
  }

  @include media-breakpoint-up(lg) {
    padding-block: 5rem;
  }

  // Container
  > .container {
    position: relative;
    z-index: 10;
  }

  // Content
  &__content {

  }

  // Heading
  &__heading {
    margin-block-end: 1.75rem;
  }

  // Rating
  &__rating-wrapper {
    margin-block-end: 1.75rem;
  }

  // Excerpt
  &__excerpt {
    margin-block-end: 1.5rem;
  }

  // CTA
  &__cta {
    margin-block-start: 3.25rem;
  }

  // Figure
  &__figure {
    margin-block-end: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;

    img {
      max-width: 80%;
      height: auto;
    }
  }



  // Info
  &__info {
    background-color: $bgc-700;
    border-radius: $border-radius-lg;
    border: var(--#{$prefix}border-width) solid var(--#{$prefix}border-color);
    padding: 1.5rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1.75rem;

    @include media-breakpoint-up(md) {
      flex-direction: row;
    }

    @include media-breakpoint-up(lg) {
      position: absolute;
      flex-direction: column;
    }

    &-figure {
      background-color: $bgc-900;
      border-radius: $border-radius;
      padding-block: 0.5rem;
      display: flex;
      width: 100%;
      align-self: flex-start;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
      margin-block-end: 0;
      padding-block: 2.25rem;
      position: relative;

      @include media-breakpoint-up(md) {
        width: 50%;
      }

      @include media-breakpoint-up(lg) {
        width: 100%;
      }

      img {
        max-width: 50%;
        height: auto;
      }
    }

    &-content {
      display: flex;
      flex-direction: column;
      gap: 1.75rem;
      padding-inline: 1rem;
      padding-block-start: 0.5rem;

      @include media-breakpoint-up(md) {
        flex: 1;
      }

      @include media-breakpoint-up(lg) {
        gap: 3.25rem;
      }
    }

    &-list {
      display: grid;
      grid-template-columns: 1fr 2fr;
      font-size: $fs-sm;
      line-height: $lh-sm;
      gap: 1.5rem;

      dt {
        font-weight: normal;
      }

      dd {
        text-align: end;
        margin-block-end: 0;
        color: $fg-0;
      }
    }
  }

  // Duotone Primary Background
  &__primary-bg {
    display: block;
    position: absolute;
    inset: 0;
    background-image: url("../../assets/images/course-title-bg.jpg");
    background-repeat: no-repeat;
    background-position: right;
    background-size: cover;

    @include media-breakpoint-up(lg) {
      inset-inline-end: 50%;
      clip-path: polygon(0 0, 100% 0%, calc(100% - 176px) 100%, 0 100%);
      inset-inline-end: calc(50% - 380px);
    }

    @include media-breakpoint-up(xl) {
      inset-inline-end: calc(50% - 442px);
    }

    @include media-breakpoint-up(xxl) {
      inset-inline-end: calc(50% - 510px);
    }
  }

  // Duotone Secondary Background
  &__secondary-bg {
    display: none;
    position: absolute;
    inset-block: 0;
    inset-inline-start: 50%;
    inset-inline-end: 0;
    background-image: url("../../assets/images/course-title-bg.jpg");
    background-repeat: no-repeat;
    background-position: left;
    background-size: cover;

    @include media-breakpoint-up(lg) {
      inset-inline-start: calc(50% + 203px);
      display: block;
      clip-path: polygon(176px 0%, 100% 0%, 100% 100%, 0% 100%);
    }

    @include media-breakpoint-up(xl) {
      inset-inline-start: calc(50% + 265px);
    }

    @include media-breakpoint-up(xxl) {
      inset-inline-start: calc(50% + 334px);
    }

    .effect-duotone__overlay {
      opacity: .45;
    }
  }
}
