//
// Lesson - Header
//

// Header
.header-lesson {
  position: fixed;
  z-index: 3;
  inset-block-start: 0;
  inset-inline: 0;
  border-block-end: 1px solid $bgc-500;
  background-color: rgba(43, 34, 54, 0.7);
  backdrop-filter: blur(8px);

  &__inner {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding-block: 1.25rem;
    width: 100%;
    row-gap: 1rem;

    @include media-breakpoint-up(lg) {
      min-height: 7rem;
      padding-block: 2rem;
    }
  }

    &__col-brand {
      // width: 50%;
      display: none;

      @include media-breakpoint-up(lg) {
        display: block;
        width: 33.3%;
      }
    }

    &__col-title {
      width: 50%;
      order: 2;

      @include media-breakpoint-up(lg) {
        order: 1;
        width: 33.3%;
      }
    }

    &__col-btns {
      width: 50%;
      order: 2;

      @include media-breakpoint-up(lg) {
        order: 2;
        width: 33.3%;
      }
    }

    &__brand {
      margin-right: $navbar-brand-margin-end;
      font-size: $fs-2xl;
      color: $navbar-dark-brand-color;
      white-space: nowrap;
      text-transform: uppercase;
      @extend %heading;
      font-style: normal;
      margin-block-end: 0;

      @include media-breakpoint-up(lg) {
        font-size: $navbar-brand-font-size;
      }

      &:hover,
      &:focus {
        color: $navbar-dark-brand-hover-color;
        text-decoration: none;
      }
    }

    &__title {
      font-size: 1rem;
      line-height: 1.2em;
      text-transform: uppercase;
      font-family: $headings-font-family;
      font-weight: bold;

      @include media-breakpoint-up(lg) {
        text-align: center;
      }
    }

    &__btns {
      display: flex;
      justify-content: flex-end;
      gap: 1rem;

      @include media-breakpoint-up(md) {
        gap: 1.5rem;
      }
    }
      &__btn {

        @include media-breakpoint-down(md) {
          padding: 0;
          width: 2.5rem;
          height: 2.5rem;
          align-items: center;
        }

        &-text {

        }
        &-icon {

        }
      }
}

// Countdown
.countdown {
  display: flex;
  align-items: center;
  gap: 0.625rem;

  &__canvas {
    width: 2rem;
    height: 2rem;
  }

  &__time {
    @extend %heading;
    font-size: 1rem;
    line-height: 1em;
    margin-block-end: 0;
  }
}
