//
// Rating
//

// Static
.rating-static {
  display: flex;
  align-items: center;
  gap: 1rem;

  &__stars {
    display: flex;
    gap: 0.25rem;

    svg {
      width: 1rem;
      height: 1rem;

      &.star--filled {
        fill: $accent;
      }
      &.star--empty {
        fill: $bgc-500;
      }
    }
  }

  &__info {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    align-items: baseline;
  }
    &__value {
      @extend %heading;
      margin-block-end: 0;
      font-size: $fs-sm;
      font-style: normal;
    }
    &__desc {
      font-size: $fs-sm;
      line-height: $lh-sm;
    }


  // Sizes
  &--lg {

    .rating-static__stars {
      display: flex;
      gap: 0.375rem;

      svg {
        width: 1.25rem;
        height: 1.25rem;
      }
    }
    .rating-static__value {
      font-size: 1.125rem;
      line-height: 1.625rem;
    }
  }
}



// Dynamic (Field)
.rating__control {

  ul {
    display: inline-flex;
    @include list-unstyled;
    margin: -0.25rem 0 0 -0.3125rem;

    li {
      position: relative;
      height: 1.625rem;
      width: 1.625rem;
      float: left;
      cursor: pointer;

      &:focus {
        outline: none;

        &::before {
          transform: scale(1);
          opacity: 0.2;
        }
      }

      &.rating__item--zero {
        display: none;
      }
    }
  }

  &--is-hidden {
    display: none;
  }
}

.rating__icon,
.rating[data-animation=on] .rating__control ul:hover .rating__icon {
  color: $accent;
}

.rating__icon--inactive,
.rating[data-animation=on] .rating__control li:hover ~ li .rating__icon,
.rating__item--checked ~ li .rating__icon {
  color: $bgc-500;
}

.rating__icon {
  overflow: hidden;
  display: inline-flex;
  height: 1.625rem;
  width: 1.625rem;
  align-items: center;
  justify-content: center;

  svg {
    display: block;
    height: 1.25rem;
    width: 1.25rem;
    max-width: 1.25rem;
    pointer-events: none;
  }
}

.rating--read-only .rating__control li {
  cursor: default;
}

.rating__icon--inactive {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.rating__item--half .rating__icon:not(.rating__icon--inactive) {
  position: relative;
  z-index: 2;
  width: 50%;
}

.rating[data-animation=on] .rating__control li:active .rating__icon {
  transform: scale(0.8);
}
.rating[data-animation=on] .rating__icon {
  transition: 0.2s;
}

.rating__select {
  display: none;
}

.rating__link {
  display: inline-block;
  transition: opacity 0.2s;

  &:hover {
    opacity: 0.85;
  }
}

/* utility classes */

.ry5-overflow-hidden {
  overflow: hidden;
}

.ry5-hide {
  display: none !important;
}
