//
// Category Box
//

.cat-box {
  display: flex;
  align-items: center;
  gap: 1.25rem;
  border: 1px solid $bgc-500;
  border-radius: $border-radius-lg;
  padding: 1.5rem;
  height: 100%;

  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 3.5rem;
    height: 3.5rem;
    border-radius: $border-radius;
    background-color: $bgc-700;
    flex-shrink: 0;
    color: $fg-0;

    svg {
      width: 2rem;
      height: 2rem;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 0.125rem;
    flex: 1;
  }

    &__title {
      font-style: normal;
      font-size: $fs-base;
      line-height: 1.75rem;
      margin-block-end: 0;
    }

    &__subtitle {
      font-size: $fs-sm;
      line-height: $lh-sm;
      margin-block-end: 0;
    }
}
