//
// Form Control
//

.form-control {

  // File input buttons theming
  &::file-selector-button {
    color: $form-file-button-color;
    font-family: $headings-font-family;
    text-transform: uppercase;
    font-style: italic;
    font-weight: bold;
  }

  &--alt {
    background-color: $bgc-800;

    &:focus {
      background-color: $bgc-800;
    }
  }
}

.form-radio-card {
  cursor: pointer;
  border: 1px solid (var(--#{$prefix}border-color));
  display: flex;
  border-radius: $border-radius;
  padding: 1rem;
  position: relative;
  overflow: hidden;

  input[type="radio"] {
    position: absolute;
    clip: rect(0,0,0,0);
    pointer-events: none;
  }

  .form-radio-card__decor {
    display: block;
    position: absolute;
    inset: 0;
    border: 1px solid transparent;
    border-radius: $border-radius;
  }

  input[type="radio"]:checked ~ .form-radio-card__decor {
    border-color: $accent;

    &::after {
      content: "";
      position: absolute;
      width: 1.75rem;
      height: 1.75rem;
      inset-block-start: 1rem;
      inset-inline-end: 1rem;
      border-radius: $border-radius-pill;
      background-color: $accent;
      display: flex;
      align-items: center;
      justify-content: center;
      background-image: escape-svg(url("data:image/svg+xml,<svg viewBox='0 0 20 20' fill='#{$fg-0}' xmlns='http://www.w3.org/2000/svg'><path fill-rule='evenodd' clip-rule='evenodd' d='M16.707 5.29303C16.8945 5.48056 16.9998 5.73487 16.9998 6.00003C16.9998 6.26519 16.8945 6.5195 16.707 6.70703L8.70701 14.707C8.51948 14.8945 8.26517 14.9998 8.00001 14.9998C7.73485 14.9998 7.48054 14.8945 7.29301 14.707L3.29301 10.707C3.11085 10.5184 3.01006 10.2658 3.01234 10.0036C3.01461 9.74143 3.11978 9.49062 3.30519 9.30521C3.4906 9.1198 3.74141 9.01464 4.00361 9.01236C4.26581 9.01008 4.51841 9.11087 4.70701 9.29303L8.00001 12.586L15.293 5.29303C15.4805 5.10556 15.7348 5.00024 16 5.00024C16.2652 5.00024 16.5195 5.10556 16.707 5.29303Z' /></svg>"));
      background-size: 1rem;
      background-position: center;
      background-repeat: no-repeat;
    }
  }

  &__content {
    display: flex;
    gap: 1rem;
    align-items: center;
  }

    &__figure {
      flex-shrink: 0;
      align-self: flex-start;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 3rem;
      aspect-ratio: 4/2.75;
      border-radius: $border-radius-sm;
      background-color: $white;

      img {
        max-width: 80%;
        height: auto;
      }

      @include media-breakpoint-up(xl) {
        width: 4rem;
      }
    }

    &__body {
      display: flex;
      flex-direction: column;
      gap: 0.125rem;
      padding-inline-end: 2rem;
      font-size: $fs-xs;
      line-height: $lh-xs;
    }
      &__title {
        font-weight: bold;
        color: $fg-0;
        font-size: $fs-sm;
      }
      &__subtitle {

      }
      &__links {
        display: flex;
        gap: 0.75rem;
        margin-block-start: 0.5rem;

        a {
          color: $fg-0;
          position: relative;
          z-index: 2;

          &:hover {
            color: $link-hover-color-dark;
          }
        }
      }
}

.form-checkbox-card {
  cursor: pointer;
  background-color: $bgc-700;
  border: 1px solid (var(--#{$prefix}border-color));
  display: flex;
  border-radius: $border-radius-lg;
  padding-block: 1.25rem;
  padding-inline: 3.75rem 1.25rem;
  position: relative;
  overflow: hidden;

  &:hover {
    background-color: $bgc-600;
  }

  input[type="checkbox"] {
    position: absolute;
    clip: rect(0,0,0,0);
    pointer-events: none;
  }

  &__decor {
    display: block;
    position: absolute;
    inset: 0;
    border: 2px solid transparent;
    border-radius: $border-radius-lg;

    &::after {
      content: "";
      position: absolute;
      width: 1.5rem;
      height: 1.5rem;
      inset-block-start: 1.25rem;
      inset-inline-start: 1.25rem;
      border-radius: $border-radius;
      background-color: $input-bg;
      border: 1px solid $bgc-500;
      display: flex;
      align-items: center;
      justify-content: center;
      background-size: 1rem;
      background-position: center;
      background-repeat: no-repeat;
      margin-block-start: -1px;
    }
  }

  input[type="checkbox"]:checked ~ .form-checkbox-card__decor {
    border-color: $accent;

    &::after {
      background-color: $accent;
      background-image: escape-svg(url("data:image/svg+xml,<svg viewBox='0 0 20 20' fill='#{$fg-0}' xmlns='http://www.w3.org/2000/svg'><path fill-rule='evenodd' clip-rule='evenodd' d='M16.707 5.29303C16.8945 5.48056 16.9998 5.73487 16.9998 6.00003C16.9998 6.26519 16.8945 6.5195 16.707 6.70703L8.70701 14.707C8.51948 14.8945 8.26517 14.9998 8.00001 14.9998C7.73485 14.9998 7.48054 14.8945 7.29301 14.707L3.29301 10.707C3.11085 10.5184 3.01006 10.2658 3.01234 10.0036C3.01461 9.74143 3.11978 9.49062 3.30519 9.30521C3.4906 9.1198 3.74141 9.01464 4.00361 9.01236C4.26581 9.01008 4.51841 9.11087 4.70701 9.29303L8.00001 12.586L15.293 5.29303C15.4805 5.10556 15.7348 5.00024 16 5.00024C16.2652 5.00024 16.5195 5.10556 16.707 5.29303Z' /></svg>"));
    }
  }

  &__content {
    display: flex;
    gap: 1rem;
    align-items: center;
  }

    &__body {
      display: flex;
      flex-direction: column;
      gap: 0.125rem;
      padding-inline-end: 2rem;
      font-size: $fs-xs;
      line-height: $lh-xs;
    }
      &__title {
        color: $fg-0;
        font-size: $fs-base;
        line-height: $lh-base;
      }
}


// Sizes based on characters
.form-control__1ch {
  width: calc(1ch + $input-padding-x * 2 + 2px);
}
.form-control__2ch {
  width: calc(2ch + $input-padding-x * 2 + 2px);
}
.form-control__3ch {
  width: calc(3ch + $input-padding-x * 2 + 2px);
}
.form-control__4ch {
  width: calc(4ch + $input-padding-x * 2 + 2px);
}
.form-control__5ch {
  width: calc(5ch + $input-padding-x * 2 + 2px);
}
.form-control__6ch {
  width: calc(6ch + $input-padding-x * 2 + 2px);
}
.form-control__7ch {
  width: calc(7ch + $input-padding-x * 2 + 2px);
}
.form-control__8ch {
  width: calc(8ch + $input-padding-x * 2 + 2px);
}
.form-control__9ch {
