//
// Stars Grid
//
.stars-grid {
  display: flex;
  flex-direction: column;
  gap: 1rem;

  &__item {
    display: grid;
    grid-template-columns: auto 1fr 1.875rem;
    align-items: center;
    gap: 0.75rem;

    &-number {
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-family: $headings-font-family;
      font-size: $fs-sm;
      line-height: 1.25rem;
      font-weight: bold;
      color: $fg-0;
      gap: 0.5rem;

      svg {
        width: 1.25rem;
        height: 1.25rem;
        color: $accent;
      }
    }

    &-progress {

    }

    &-value {
      text-align: end;
      font-family: $headings-font-family;
      font-size: $fs-sm;
      line-height: 1.25rem;
      color: $fg-0;
    }
  }
}
