//
// Comments
//
.comments {

  .card-body & {
    margin-block: calc(var(--#{$prefix}card-spacer-y) * -1);
  }
}

.comment {
  display: flex;
  flex-direction: column;

  & + & {
    border-block-start: var(--#{$prefix}border-width) solid var(--#{$prefix}border-color);
  }

  &-inner {
    display: flex;
    gap: 1rem;
  }

  .card-body & {
    margin-inline: calc(var(--#{$prefix}card-spacer-x) * -1);
    padding-inline: var(--#{$prefix}card-spacer-x);
    padding-block: 1.5rem;
  }

  &__figure {
    flex-shrink: 0;
  }
    &__avatar {
      border-radius: $border-radius-pill;
      overflow: hidden;
      width: 3rem;
      height: 3rem;
      object-fit: cover;
    }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
  }

    &__header {
      display: flex;
      flex-direction: column;
      font-size: $fs-sm;
      line-height: $lh-sm;
      gap: 0.125rem;
      padding-block-start: 0.25rem;
    }

      &__author {
        @extend %heading;
        font-size: 1rem;
        line-height: 1.375rem;
        color: $fg-0;
        font-style: normal;
      }
      &__origin {

        a {
          color: $fg-0;

          &:hover {
            color: $link-hover-color-dark;
          }
        }
      }

      &__date {
        font-size: $fs-xs;
        line-height: $lh-xs;
        color: $fg-300;
      }

    &__body {
      font-size: $fs-sm;
      line-height: $lh-sm;
    }

  &__footer {
    font-size: $fs-sm;
    line-height: $lh-sm;
    display: flex;
    flex-wrap: wrap;
    gap: 0.375rem;
    color: $fg-0;
    padding-block-start: 0.5rem;

    @include media-breakpoint-up(lg) {
      gap: 1rem;
    }
  }
    &__link {
      display: inline-flex;
      align-items: center;
      gap: 0.375rem;

      &--read {

      }

      &--comment {
        color: $fg-0;

        &:hover {
          color: $link-hover-color-dark;
        }
      }
    }

  // Child comments
  .comment {
    background-color: $bgc-900;
    border-block: var(--#{$prefix}border-width) solid var(--#{$prefix}border-color);
    margin-block-start: var(--#{$prefix}card-spacer-y);
    margin-block-end: calc(var(--#{$prefix}card-spacer-y) * -1);

    @include media-breakpoint-up(lg) {
      padding-inline-start: 5.75rem;
    }
  }
}
