//
// Account Profile Title
//
.account-profile-title {

  &__bg {
    background-image: url("../../assets/images/page-title-bg.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 4rem;
    border-block-end: 1px solid (var(--#{$prefix}border-color));

    @include media-breakpoint-up(lg) {
      height: 8rem;
    }

    @include media-breakpoint-up(xxl) {
      height: 11rem;
    }
  }

  // Primary
  &__primary {
    margin-block-start: -1.5rem;
    position: relative;
    z-index: 2;

    @include media-breakpoint-up(lg) {
      margin-block-start: -2.25rem;
    }
  }
    &__info {
      display: flex;
      flex-direction: column;

      @include media-breakpoint-up(lg) {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
      }
    }

      &__body {
        display: flex;
        gap: 1rem;
        align-items: flex-start;

        @include media-breakpoint-up(lg) {
          gap: 1.75rem;
          align-items: center;
        }

        @include media-breakpoint-up(xl) {
          gap: 2.25rem;
        }
      }
        &__avatar-wrapper {
          position: relative;
          width: 6rem;
          height: 6rem;
          flex-shrink: 0;

          @include media-breakpoint-up(lg) {
            width: 9rem;
            height: 9rem;
          }

          @include media-breakpoint-up(xl) {
            width: 11rem;
            height: 11rem;
          }
        }

          &__circle-bar {
            position: absolute;
            width: 100%;
            height: 100%;
            margin: 0;
            z-index: 2;
            fill: none;
            stroke-width: 10;

            @include media-breakpoint-up(lg) {

            }

            .circle-bar-placeholder {
              stroke: $bgc-500;
            }
            .circle-bar-fill {
              stroke: url(#GradientColor);
              stroke-width: 10;
              stroke-dasharray: 521.5; // C=2r
              stroke-dashoffset: 130.375; // 75% (521.5 * 0.25)
              stroke-linecap: round;
              transform: rotate(-90deg);
              transform-origin: center;
            }
          }

          &__avatar {
            position: absolute;
            inset-block-start: 50%;
            inset-inline-start: 50%;
            transform: translate(-50%, -50%);
            border-radius: $border-radius-pill;
            max-width: 84%;
            height: auto;
            object-fit: cover;
          }
          &__badge {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            inset-block-end: 0;
            inset-inline-end: 0;
            width: 2rem;
            height: 2rem;
            border-radius: $border-radius-pill;
            background-color: $bgc-600;
            font-size: 1rem;
            line-height: 1em;
            color: $fg-0;
            z-index: 2;
            font-family: $headings-font-family;
            font-weight: bold;

            @include media-breakpoint-up(lg) {
              width: 3rem;
              height: 3rem;
              font-size: 1.25rem;
            }

            @include media-breakpoint-up(xl) {
              width: 3.5rem;
              height: 3.5rem;
              font-size: 1.5rem;
            }
          }

          &__heading {
            padding-block-start: 2rem;

            @include media-breakpoint-up(lg) {
              padding-block-start: 1.75rem;
            }
          }
            &__name {
              text-transform: none;
              font-style: normal;
              margin-block-end: 0.5rem;
              font-size: 1.5rem;
              line-height: 1.2em;

              @include media-breakpoint-up(lg) {
                font-size: 2rem;
              }

              @include media-breakpoint-up(xl) {
                font-size: 2.625rem;
                line-height: 2.875rem;
              }
            }

            &__desc {
              @include media-breakpoint-down(lg) {
                font-size: $fs-sm;
                line-height: $lh-sm;
              }
            }

      &__cta {
        padding-block-start: 1.5rem;
        display: flex;
        flex-wrap: wrap;
        gap: 1rem;

        @include media-breakpoint-up(lg) {
          padding-block-start: 2.25rem;
        }
      }

  // Secondary (Stats)
  &__secondary {
    margin-block-start: 1.5rem;

    @include media-breakpoint-up(lg) {
      margin-block-start: 2.25rem;
    }
  }
}
