//
// Lesson - Footer
//

// Footer
.footer-lesson {
  position: fixed;
  inset-inline: 0;
  inset-block-end: 0;
  padding-block-end: 0.75rem;
  z-index: 3;

  @include media-breakpoint-up(lg) {
    padding-block-end: 4rem;
  }
}
.footer-lesson-overlay {
  display: block;
  position: absolute;
  inset-block-end: 0;
  inset-inline: 0;
  height: 10rem;
  background-image: linear-gradient(180deg, rgba(43, 34, 54, 0.00) 0%, rgba(43, 34, 54, 0.70) 10.97%, rgba(43, 34, 54, 0.90) 23.33%, #2B2236 35.96%);
  z-index: 1;
  pointer-events: none;

  @include media-breakpoint-up(lg) {
    height: 19.25rem;
  }
}

.lesson-progress {
  position: relative;
  z-index: 2;

  &__heading {
    display: flex;
    justify-content: center;
    margin-block-end: .75rem;
  }

  &__label {
    @extend %heading;
    font-size: 1rem;
    line-height: 1.5em;
    margin-block-end: 0;
    text-transform: uppercase;
  }

  &__inner {
    border-radius: 1rem;
    background-color: $bgc-700;
    border: 1px solid $bgc-500;
    padding: 0.75rem;

    @include media-breakpoint-up(md) {
      padding: 1rem;
    }

    @include media-breakpoint-up(lg) {
      min-height: 5rem;
    }
  }

  // Navigation
  &__nav {
    display: flex;
    flex-wrap: wrap;
    column-gap: 1.25rem;
    row-gap: 1rem;

    @include media-breakpoint-up(md) {
      row-gap: 1.5rem;
    }

    @include media-breakpoint-up(lg) {
      column-gap: 2rem;
    }

    &-prev {

      @include media-breakpoint-down(lg) {
        order: 2;
      }

    }

    &-progress-wrapper {
      flex: 1;
      display: flex;
      align-items: center;

      @include media-breakpoint-down(lg) {
        width: 100%;
        flex-basis: 100%;
        order: 1;
        padding-block-start: 0.5rem;
      }
    }

      // Progress
      &-progress {
        width: 100%;
        display: flex;
        align-items: center;
        gap: 0.1875rem;

        @include media-breakpoint-up(md) {
          gap: 0.25rem;
        }

        &-item {
          flex: 1;
          height: 0.5rem;
          border-radius: $border-radius;
          background-color: $bgc-500;
          transition: all 0.2s ease;

          &:hover {
            box-shadow: 0 0 0 2px rgba($primary-300, 0.5);
          }

          // Solved
          &--solved {
            background-color: $primary-300;
          }

          // Skipped
          &--skipped {
            background-color: $fg-200;
          }

          // Current
          &--current {
            background-color: $accent-300;
          }
        }
      }

    &-next {
      display: flex;
      gap: .75rem;

      @include media-breakpoint-down(lg) {
        order: 3;
        flex: 1;
        justify-content: flex-end;
      }
    }

      // Buttons
      &-btn {

        &--prev {

          @include media-breakpoint-down(lg) {
            width: 2.5rem !important;
            height: 2.5rem !important;
          }

          svg {
            width: 1.25rem !important;
            height: 1.25rem !important;
            stroke: $fg-0 !important;
          }
        }

        &--next {

          @include media-breakpoint-down(lg) {
            padding-inline: 1.25rem;
            padding-block: 0.625rem;
            font-size: $fs-sm;
            align-items: center;

            svg {
              width: 0.875rem !important;
              height: 0.875rem !important;
            }
          }

          svg {
            margin-inline-end: -0.5rem;
          }
        }

        &--skip {
          @include media-breakpoint-down(lg) {
            padding-inline: 1.25rem;
            padding-block: 0.625rem;
            font-size: $fs-sm;
            align-items: center;

            svg {
              width: 0.875rem !important;
              height: 0.875rem !important;
            }
          }
        }
      }

  }
}
