//
// Swiper
//
.swiper-nav {
  display: flex;
  gap: 0.5rem;

  @include media-breakpoint-up(lg) {
    gap: 0.75rem;
  }

  &__btn {
    width: 2.25rem;
    height: 2.25rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $bgc-700;
    border: var(--#{$prefix}border-width) solid var(--#{$prefix}border-color);
    border-radius: $border-radius;
    transition: all .2s ease;

    @include media-breakpoint-up(lg) {
      width: 3rem;
      height: 3rem;
    }

    svg {
      width: 1rem;
      height: 1rem;
      color: $fg-0;

      @include media-breakpoint-up(lg) {
        width: 1.25rem;
        height: 1.25rem;
      }
    }

    &:hover {
      background-color: $accent;
      border-color: $accent-300;
    }
  }
}

