//
// VM Title
//

.vm-title {
  background-image: url("../../assets/images/vm-title-bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-block: 2rem;

  @include media-breakpoint-up(md) {
    padding-block: 3rem;
  }

  @include media-breakpoint-up(lg) {
    padding-block: 5rem;
  }

  // Container
  > .container {
    position: relative;
    z-index: 10;
  }

  // Figure
  &__figure-wrapper {
    background-color: $bgc-700;
    border-radius: $border-radius-lg;
    border: var(--#{$prefix}border-width) solid var(--#{$prefix}border-color);
    padding: 1.5rem;
  }
    &__figure {
      background-color: $bgc-900;
      border-radius: $border-radius;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 1.5rem;
      margin: 0;
      width: 100%;
      aspect-ratio: 1 / 1;

      img {
        max-width: 66%;
        height: auto;
      }
    }

  // Content
  &__content {
    display: flex;
    flex-direction: column;
    gap: 1rem;

    @include media-breakpoint-up(lg) {
      padding-inline-start: 1.5rem;
    }

    @include media-breakpoint-up(xl) {
      padding-inline-start: 2.75rem;
      padding-block-start: 3rem;
    }
  }

    // Excerpt
    &__excerpt {
      padding-block-start: .75rem;
      padding-block-end: .5rem;
    }

    // Info
    &__info {
      display: flex;
      flex-wrap: wrap;
      gap: 1rem;
      margin-block-end: 0;
      padding-block-start: 0.5rem;

      @include media-breakpoint-up(xl) {
        gap: 1.5rem;
      }

      &-item {
        display: flex;
        gap: .5rem;
        align-items: center;

        svg {
          width: 1.25rem;
          height: 1.25rem;
          stroke: $fg-200;

          @include media-breakpoint-up(xl) {
            width: 1.5rem;
            height: 1.5rem;
          }

          &.svg-fill {
            stroke: none;
            fill: $fg-200;
          }
        }

        &-label {
          color: $fg-0;
          margin-block-end: 0;
          font-size: $fs-sm;
          line-height: $lh-sm;
        }
      }
    }

    // CTA
    &__cta {
      padding-block-start: 2.25rem;
    }
}
