//
// Badges
//

.badge {
  @extend %heading;
  line-height: 1.4em;
  text-transform: uppercase;
  vertical-align: top;

  // Empty badges collapse automatically
  &:empty {
    display: none;
  }
}

.badge--lg {
  font-size: $badge-font-size-lg;
  line-height: 1.33em;
  padding-inline: $badge-padding-x-lg;
  padding-block: $badge-padding-y-lg;
}

// Quick fix for badges in buttons
.btn .badge {
  position: relative;
  top: -1px;
}

// Badges Outline
.badge-outline {
  border: 1px solid transparent;
}
