//
// Iconbox
//
.iconbox {

  &__icon {
    border: var(--#{$prefix}border-width) solid var(--#{$prefix}border-color);
    border-radius: $border-radius-pill;
    width: 3rem;
    height: 3rem;
    margin-block-end: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;

    @include media-breakpoint-up(lg) {
      width: 3.5rem;
      height: 3.5rem;
      margin-block-end: 2rem;
    }

    svg {
      color: $fg-0;
      width: 1.25rem;
      height: 1.25rem;

      @include media-breakpoint-up(lg) {
        width: 1.5rem;
        height: 1.5rem;
      }
    }
  }

  &__body {

    @include media-breakpoint-up(lg) {
      padding: 2rem;
    }
  }

  // Iconbox Alt
  &--alt {
    background-color: $bgc-600;

    .iconbox__icon {
      background-color: $bgc-900;
    }
  }

  // Small
  &--sm {

    .iconbox__icon {
      margin-block-end: 1.25rem;

      @include media-breakpoint-up(lg) {
        margin-block-end: 1.25rem;
      }

      @include media-breakpoint-up(xl) {
        margin-block-end: 2rem;
      }
    }

    .iconbox__body {
      padding: 1.25rem;

      @include media-breakpoint-up(lg) {
        padding: 1.5rem;
      }
    }

    .iconbox__title {
      font-size: $h5-font-size;
      line-height: $h5-line-height;
    }

    .iconbox__desc {
      font-size: $fs-sm;
      line-height: $lh-sm;
    }
  }
}
