//
// Headings
//
h1, .h1 {
  line-height: $h1-line-height;
  text-transform: $headings-text-transform;
}

h2, .h2 {
  line-height: $h2-line-height;
  text-transform: $headings-text-transform;
}

h3, .h3 {
  line-height: $h3-line-height;
  text-transform: $headings-text-transform;
}

h4, .h4 {
  line-height: $h4-line-height;
  text-transform: $headings-text-transform;
}

h5, .h5 {
  line-height: $h5-line-height;
  text-transform: $headings-text-transform;
}

h6, .h6 {
  line-height: $h6-line-height;
  text-transform: $headings-text-transform;
}

.text-xs {
  font-size: $fs-xs;
  line-height: $lh-xs;
}

.text-sm {
  font-size: $fs-sm;
  line-height: $lh-sm;
}

.text-base {
  font-size: $fs-base;
  line-height: $lh-base;
}

.text-lg {
  font-size: $fs-lg;
  line-height: $lh-lg;
}

.text-xl {
  font-size: $fs-xl;
  line-height: $lh-xl;
}

.text-2xl {
  font-size: $fs-2xl;
  line-height: $lh-2xl;
}

.text-3xl {
  font-size: $fs-3xl;
  line-height: $lh-3xl;
}

.text-4xl {
  font-size: $fs-4xl;
  line-height: $lh-4xl;
}

.text-5xl {
  font-size: $fs-5xl;
  line-height: $lh-5xl;
}

.text-6xl {
  font-size: $fs-6xl;
  line-height: $lh-6xl;
}


// Text Highlight
.text-highlight {
  color: $fg-0;
}

// Hgroup
.hgroup {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  margin-block-end: 1.25rem;

  &__eyebrow {
    font-size: $fs-xs;
    line-height: $lh-xs;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    font-weight: bold;
    font-family: $headings-font-family;
  }

  &__heading {
    margin-block-end: 0;
  }
}

// Spacers
.spacer {
  height: $spacer;
}

// List
.list-check {

  > li {
    position: relative;
    padding-inline-start: 1.75rem;

    &::before {
      content: "";
      display: block;
      position: absolute;
      inset-inline-start: 0;
      inset-block-start: 0.25rem;
      width: 1rem;
      height: 1rem;
      background-image: escape-svg(url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M13.3659 4.23423C13.5159 4.38425 13.6002 4.5877 13.6002 4.79983C13.6002 5.01196 13.5159 5.21541 13.3659 5.36543L6.96593 11.7654C6.8159 11.9154 6.61246 11.9997 6.40033 11.9997C6.18819 11.9997 5.98475 11.9154 5.83473 11.7654L2.63473 8.56543C2.489 8.41455 2.40836 8.21247 2.41019 8.00271C2.41201 7.79295 2.49614 7.5923 2.64447 7.44397C2.7928 7.29565 2.99345 7.21151 3.2032 7.20969C3.41296 7.20787 3.61504 7.2885 3.76593 7.43423L6.40033 10.0686L12.2347 4.23423C12.3847 4.08425 12.5882 4 12.8003 4C13.0125 4 13.2159 4.08425 13.3659 4.23423Z' fill='#{$accent}'/%3E%3C/svg%3E%0A"));
      background-repeat: no-repeat;
      background-position: center center;
    }
  }
}


// Pre
pre {
  background-color: $bgc-950;
  border-radius: $border-radius;
  padding-inline: 1.5rem;
  padding-block: 1.25rem;
  line-height: 1.5rem;
}
