//
// Lesson - Title
//

.lesson-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 0.5rem;
  margin-block-end: 2rem;

  @include media-breakpoint-up(lg) {
    gap: $spacer;
    margin-block-end: 3.5rem;
  }

  &__heading {
    text-transform: none;
    font-style: normal;
    margin-block-end: 0;
    font-weight: 500;
  }

  &__desc {

  }
}
