//
// Lessons list
//
.lessons-list {
  @include list-unstyled;
  margin-block-end: 0;

  .accordion & {
    margin-block: calc($accordion-padding-y * -1);
    margin-inline: calc($accordion-padding-x * -1);
  }

  &__item {
    position: relative;

    .accordion & {
      background-color: $bgc-800;
      border-bottom: var(--#{$prefix}accordion-border-width) solid var(--#{$prefix}accordion-border-color);

      &:last-child {
        border-bottom: 0;
      }
    }

    // Current
    &--current {

      &::before {
        content: "";
        display: block;
        position: absolute;
        inset-inline-start: 0;
        inset-block: 0;
        width: 0.25rem;
        background-color: $primary-300;
        z-index: 2;
        pointer-events: none;
      }

      .lessons-list__link {
        background-color: $bgc-700;
      }
    }

    // Hidden
    &--hidden {
      pointer-events: none;

      .lessons-list__link,
      .lessons-list__item-icon,
      .lessons-list__item-title {
        color: $fg-300;
      }
    }

    &-icon {
      width: 1.25rem;
      height: 1.25rem;
      display: block;
      position: absolute;
      inset-inline-start: 1.5rem;
      inset-block-start: 1.5625rem;
      color: $fg-0;
    }

    &-title {
      color: $fg-0;
      font-family: $headings-font-family;
      line-height: 1.5rem;
    }

    &-info {
      display: flex;
      align-items: center;
      gap: 0.5rem;

      @include media-breakpoint-up(sm) {
        gap: 1.25rem;
      }
    }

      &-meta {
        font-size: $fs-sm;
        line-height: $lh-sm;
        color: $fg-200;
      }
  }

  &__link {
    display: flex;
    justify-content: space-between;
    padding-inline-start: 2.25rem;
    padding-inline-end: 1.5rem;
    padding-block: 1.5rem;
    align-items: center;
    position: relative;

    &:hover {
      background-color: $bgc-700;
    }

    .accordion & {
      padding-inline-start: 3.75rem;
    }
  }
}
