//
// Testimonials
//

.testimonials {

}

  .testimonial {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    padding: 1.75rem;
    border: 1px solid $bgc-500;
    border-radius: $border-radius-xl;

    &__rating {

    }

    &__content {

    }

    &__author {
      display: flex;
      align-items: center;
      gap: 1rem;
      padding-block-start: 0.5rem;

      &-avatar {
        margin: 0;
        width: 3rem;
        height: 3rem;
        overflow: hidden;
        flex-shrink: 0;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }

      &-info {

      }

      &-name {
        font-size: 1rem;
        line-height: 1.5em;
        margin-block-end: 0;
      }
      &-position {
        font-size: $fs-sm;
        line-height: $lh-sm;
      }
    }

  }
