//
// CTA
//

.cta {
  background-image: url("../../assets/images/cta-bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-block: 5rem;

  @include media-breakpoint-up(lg) {
    padding-block: 6rem;
  }

  @include media-breakpoint-up(xl) {
    padding-block: 8rem;
  }

  // Container
  > .container {
    position: relative;
    z-index: 10;
  }

  &__inner {

  }
    &__title {
      text-transform: none;
      font-style: normal;
      font-weight: normal;
    }
}
