//
// Page Title
//

.page-title {
  background-image: url("../../assets/images/page-title-bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-block: 2rem;

  @include media-breakpoint-up(sm) {
    padding-block: 3.5rem;
  }

  // Container
  > .container {
    position: relative;
    z-index: 10;
  }

  // Inner
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 0.5rem;
  }

  // Heading
  &__heading {
    margin: 0;
  }

  // Breadcumb Wrapper
  &__breadcrumb-wrapper {

  }

  // Breadcumb
  .breadcrumb {
    a {
      color: $fg-0;

      &:hover {
        color: $accent;
      }
    }
  }

  // Variations: Horizontal
  &--horizontal {

    .page-title__inner {

      @include media-breakpoint-up(sm) {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
      }
    }
  }


  // Variations: Vertical
  &--vertical {

    .page-title__inner {

      @include media-breakpoint-up(sm) {
        row-gap: 1rem;
      }
    }
  }
}
