.features-box {
  background-color: rgba($bgc-700, 70%);
  border-radius: 1rem;
  padding: 1.5rem;

  @include media-breakpoint-up(md) {
    padding: 2rem;
  }

  @include media-breakpoint-up(lg) {
    padding: 3rem;
  }

  @include media-breakpoint-up(xl) {
    padding: 4rem;
  }
}
