//
// Info List
//
.info-list {
  display: grid;
  gap: 0.25rem 1rem;
  color: $fg-0;
  font-size: $fs-xs;
  line-height: $lh-xs;

  @include media-breakpoint-up(sm) {
    grid-template-columns: 6rem 1fr;
    gap: 1.5rem 1rem;
    font-size: $fs-sm;
    line-height: $lh-sm;
  }

  @include media-breakpoint-up(lg) {
    grid-template-columns: 1fr;
    gap: 0.25rem 1rem;
  }

  @include media-breakpoint-up(xxl) {
    grid-template-columns: 6rem 1fr;
    gap: 1.5rem 1rem;
  }

  dt {

  }

  dd {
    margin-block-end: 0.75rem;

    @include media-breakpoint-up(sm) {
      margin-block-end: 0;
    }

    @include media-breakpoint-up(lg) {
      margin-block-end: 1rem;
    }

    @include media-breakpoint-up(xxl) {
      margin-block-end: 0;
    }

    &:last-child {
      margin-block-end: 0;

      @include media-breakpoint-up(sm) {
        margin-block-end: 0;
      }

      @include media-breakpoint-up(lg) {
        margin-block-end: 0;
      }
    }
  }
}
