//
// Lesson - Content
//

// Content
.content-wrapper-lesson {
  padding-block-start: 7.5rem;
  padding-block-end: 11.5rem;

  @include media-breakpoint-up(lg) {
    padding-block-start: 15rem;
    padding-block-end: 19rem;
  }
}

// Evaluation

// Multiple Choice Exam
.lesson-multiple-choice {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;

  @include media-breakpoint-up(lg) {
    gap: 1rem;
  }
}

// Fill-in Advanced
.lesson-fill-in-advanced-text {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
  align-items: center;
  font-size: $fs-lg;
  line-height: $lh-lg;

  .form-control {
    display: inline-flex;
  }
}

// Scripting Excercise
.lesson-scripting-exercise {

}

// Draggable
.draggable {

  // Labels
  &-label {
    min-height: 4.125rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-style: $fs-xl;
    line-height: $lh-xl;
    color: $fg-0;
    font-weight: bold;
    font-family: $headings-font-family;
  }

  // Zone
  &-zone {
    height: 100%;
    min-height: 4.125rem;
    position: relative;

    &::before {
      content: "";
      display: block;
      position: absolute;
      inset: 0;
      border: 2px dashed $bgc-500;
      border-radius: $border-radius-lg;
    }

    &::after {
      content: "Drop here to add your answer";
      display: block;
      position: absolute;
      opacity: 0;
      inset-block-start: 50%;
      inset-inline: 0;
      padding-inline: 1rem;
      text-align: center;
      transform: translateY(-50%);
      font-size: $fs-xs;
      line-height: $lh-xs;
      color: $accent;
      z-index: 1  ;
    }

    &.over {
      background-color: rgba($accent, 0.05);

      &::before {
        border-color: $accent;
      }
      &::after {
        opacity: 1;
      }
    }
  }

  // Options
  &-options {
    display: grid;
    gap: 1rem;
    grid-template-columns: 1fr;

    @include media-breakpoint-up(sm) {
      grid-template-columns: repeat(2, 1fr);
    }

    @include media-breakpoint-up(lg) {
      grid-template-columns: repeat(3, 1fr);
    }

    @include media-breakpoint-up(xl) {
      grid-template-columns: repeat(4, 1fr);
    }

    &__holder {
      position: relative;
      min-height: 4.125rem;

      &::before {
        content: "";
        position: absolute;
        inset: 0;
        border: 2px dashed $bgc-500;
        border-radius: $border-radius-lg;
      }
    }

      &__option {
        background-color: $bgc-700;
        border: 1px solid $bgc-500;
        border-radius: $border-radius-lg;
        position: relative;
        align-items: center;
        justify-content: center;
        text-align: center;
        display: flex;
        min-height: 4.125rem;
        height: 100%;
        padding-inline: 2.5rem;
        padding-block: 0.75rem;
        color: $fg-0;
        overflow: hidden;
        cursor: move;
        z-index: 2;

        &::before {
          content: "";
          width: 0.375rem;
          position: absolute;
          inset-block: 0;
          inset-inline-start: 0;
          background-color: $bgc-500;
        }

        &::after {
          content: "";
          display: block;
          position: absolute;
          inset-inline-end: 1.25rem;
          inset-block-start: 50%;
          transform: translateY(-50%);
          width: 0.75rem;
          height: 1.25rem;
          background-image: url("data:image/svg+xml,%3Csvg width='12' height='22' viewBox='0 0 12 22' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.74966 4.29216C2.71597 4.29216 3.49932 3.50881 3.49932 2.54251C3.49932 1.5762 2.71597 0.792847 1.74966 0.792847C0.783349 0.792847 0 1.5762 0 2.54251C0 3.50881 0.783349 4.29216 1.74966 4.29216Z' fill='white'/%3E%3Cpath d='M10.2506 4.29216C11.2169 4.29216 12.0003 3.50881 12.0003 2.54251C12.0003 1.5762 11.2169 0.792847 10.2506 0.792847C9.28433 0.792847 8.50098 1.5762 8.50098 2.54251C8.50098 3.50881 9.28433 4.29216 10.2506 4.29216Z' fill='white'/%3E%3Cpath d='M1.74966 12.7494C2.71597 12.7494 3.49932 11.9661 3.49932 10.9998C3.49932 10.0335 2.71597 9.25012 1.74966 9.25012C0.783349 9.25012 0 10.0335 0 10.9998C0 11.9661 0.783349 12.7494 1.74966 12.7494Z' fill='white'/%3E%3Cpath d='M10.2506 12.7494C11.2169 12.7494 12.0003 11.9661 12.0003 10.9998C12.0003 10.0335 11.2169 9.25012 10.2506 9.25012C9.28433 9.25012 8.50098 10.0335 8.50098 10.9998C8.50098 11.9661 9.28433 12.7494 10.2506 12.7494Z' fill='white'/%3E%3Cpath d='M1.74966 21.2071C2.71597 21.2071 3.49932 20.4237 3.49932 19.4574C3.49932 18.4911 2.71597 17.7078 1.74966 17.7078C0.783349 17.7078 0 18.4911 0 19.4574C0 20.4237 0.783349 21.2071 1.74966 21.2071Z' fill='white'/%3E%3Cpath d='M10.2506 21.2071C11.2169 21.2071 12.0003 20.4237 12.0003 19.4574C12.0003 18.4911 11.2169 17.7078 10.2506 17.7078C9.28433 17.7078 8.50098 18.4911 8.50098 19.4574C8.50098 20.4237 9.28433 21.2071 10.2506 21.2071Z' fill='white'/%3E%3C/svg%3E%0A");
          background-size: 0.75rem 1.25rem;
          background-position: center;
          background-repeat: no-repeat;
        }

        // dragging
        &.dragging {
          opacity: 1;
          border-color: $accent;

          &::before {
            background-color: $accent;
          }
        }
      }
  }
}
