//
// Virtual Machine
//

.vm {

  // VM: Block
  &--block {

    .vm__inner {
      display: flex;
      gap: 1.5rem;
      flex-direction: column;

      @include media-breakpoint-up(sm) {
        flex-direction: row;
        align-items: stretch;
      }

      @include media-breakpoint-up(lg) {
        gap: 2.25rem;
      }
    }
      .vm__figure {
        background-color: $bgc-900;
        border-radius: $border-radius;
        padding-block: 0.5rem;
        display: flex;
        width: 100%;
        align-self: flex-start;
        align-items: center;
        justify-content: center;
        flex-shrink: 0;
        margin-block-end: 0;
        padding-block: 2.25rem;
        position: relative;

        @include media-breakpoint-up(sm) {
          flex-basis: 7rem;
          padding-block: 1.5rem;
        }

        @include media-breakpoint-up(lg) {
          flex-basis: 14rem;
          padding-block: 2.25rem;
          align-self: stretch;
        }

        @include media-breakpoint-up(xl) {
          flex-basis: 17.375rem;
        }

        img {
          max-width: 60%;
          height: auto;
        }

        .badge {
          position: absolute;
          inset-inline-start: -.875rem;
          inset-block-start: .625rem;
        }
      }

      .vm__content {
        display: flex;
        flex-direction: column;
        gap: 2.25rem;
      }

        .vm__body {
          display: flex;
          flex-direction: column;
          gap: 1.25rem;
        }

          .vm__heading {
            display: flex;
            flex-direction: column;
            gap: 0.75rem;
          }
            .vm__title {
              margin-block-end: 0;
            }


        .vm__cta {
          flex-shrink: 0;
          display: flex;
          flex-wrap: wrap;
          gap: .75rem;
        }

      .card-footer {
        padding-block: 1.25rem;
        display: flex;
        flex-direction: column;
        row-gap: 2rem;

        @include media-breakpoint-up(lg) {
          flex-direction: row;
          justify-content: space-between;
        }

        @include media-breakpoint-down(md) {
          .progress-bars .progress-bars__holder {
            width: auto;
            flex: 1;
          }
        }
      }

        .vm__info {
          display: flex;
          flex-wrap: wrap;
          gap: 1rem;
          margin-block-end: 0;

          @include media-breakpoint-up(xl) {
            gap: 1.5rem;
          }

          &-item {
            display: flex;
            gap: .375rem;
            align-items: center;

            @include media-breakpoint-up(xl) {
              gap: .5rem;
            }

            svg {
              width: 1.25rem;
              height: 1.25rem;
              stroke: $fg-200;

              @include media-breakpoint-up(xl) {
                width: 1.5rem;
                height: 1.5rem;
              }
            }

            &-label {
              @extend %heading;
              margin-block-end: 0;
              font-size: $fs-sm;
              font-style: normal;
              line-height: 1rem;
              text-transform: uppercase;

              @include media-breakpoint-up(xl) {
                font-size: $fs-sm;
                line-height: 1.125rem;
              }
            }
          }
        }
  }

  // VM: Card (sm)
  &--card-sm {
    background-color: $bgc-600;
    border-radius: $border-radius-lg;

    .vm__header {
      padding: 1.5rem;
      display: flex;
      gap: 0.75rem;
      align-items: center;
      border-block-end: 1px solid (var(--#{$prefix}border-color));
    }
      .vm__figure {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 3rem;
        height: 3rem;
        background-color: $bgc-900;
        border-radius: $border-radius;
        margin-block-end: 0;
        flex-shrink: 0;

        img {
          max-width: 66.6%;
          height: auto;
        }
      }

      .vm__title {
        min-width: 0;
        font-size: 1rem;
        line-height: 1.5rem;
        @include text-truncate;
        margin-block-end: 0;

        a {
          color: $fg-0;

          &:hover {
            color: $link-hover-color-dark;
          }
        }
      }

    .vm__content {
      padding-inline: 1.5rem;
      font-size: $fs-xs;
      line-height: $lh-xs;
    }
      .vm__info {

      }
        .vm__info-item {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding-block: 1rem;
        }
        .vm__info-item + .vm__info-item {
          border-block-start: 1px solid (var(--#{$prefix}border-color));
        }
          .vm__info-value--highlight {
            color: $fg-0;
          }

    .vm__progress {
      display: flex;
      align-items: center;
      gap: 0.5rem;

      @include media-breakpoint-up(sm) {
        gap: 0.75rem;
      }

      &-label {
        font-size: $fs-xs;
        line-height: $lh-xs;
        white-space: nowrap;
      }

      &-bar {
        width: 1.5rem;
        height: 1.5rem;
        fill: none;

        .circle-bar-placeholder {
          stroke: $bgc-500;
          stroke-width: 4px;
        }
        .circle-bar-fill {
          stroke: $accent;
          stroke-width: 3;
          stroke-dasharray: 62; // C=2r
          // stroke-dashoffset: 46.5; // 25% (62 * 0.75)
          stroke-linecap: round;
          transform: rotate(-90deg);
          transform-origin: center;
        }
      }
    }
  }
}


// VMs
.vms {

  &__title {
    font-style: normal;
    color: var(--#{$prefix}body-color);
    font-weight: normal;
    font-size: $fs-sm;
    line-height: 1.5rem;
    margin-block-end: 0.5rem;

    &:not(:first-child) {
      margin-block-start: 1.5rem;
    }
  }

  &__list {

  }
}
