//
// Rank Change
//
.rank-change {
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.25rem;

  &--up .rank-change__symbol,
  &--down .rank-change__symbol {
    width: .75rem;
    height: .5rem;
  }

  &--up .rank-change__symbol {
    background-color: $success;
    clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
  }
  &--down .rank-change__symbol {
    background-color: $danger;
    clip-path: polygon(100% 0, 0 0, 50% 100%);
  }

  &--equal {
    gap: 0.125rem;
  }

  &--equal .rank-change__symbol {
    color: $fg-0;
    font-weight: bold;

    &::before {
      content: "=";
    }
  }

  &__value {
    font-size: $fs-xs;
    line-height: $lh-xs;
    color: $fg-0;
  }
}
